import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom'
import Slider from '../Slider'
import Tmeland from '../Tmeland'
import Qqmusic from '../Qqmusic'
import Fanlive from '../Fanlive'
import Hiphop from '../Hiphop'
import Inventory from '../Inventory'
// import { Outlet } from 'react-router-dom'

export default class Portfolio extends Component {
  render() {
    return (
      <>
        {/* <Outlet/> */}
        <Switch>
          <Route path="/portfolio/tmeland" component={Tmeland}></Route>
          <Route path="/portfolio/qqmusic" component={Qqmusic}></Route>
          <Route path="/portfolio/fanlive" component={Fanlive}></Route>
          <Route path="/portfolio/hiphop" component={Hiphop}></Route>
          <Route path="/portfolio/inventory" component={Inventory}></Route>
        </Switch>
        <Slider/>
      </>
    )
  }
}
