import React, { Component } from 'react'
import gsap from 'gsap'
import { withRouter } from 'react-router-dom'
import { portfolioData } from '../../data/portfolioData'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"

class SliderDom extends Component {

    state={
        link:"/project/tmeland"
    }

    //初始化
    componentDidMount() {

        // 拆分标题为单个文字()
        Splitting({ target: document.querySelectorAll(".page_title__line"), by: 'chars' })
        this.currId=0
        this.nextId=0
        this.length=portfolioData.length
        this.tl = gsap.timeline()
        // this.tl.set(".page_title__section .page_title__line .char", { y: '100%' })
        //     .set(".home_pagination_el", { y: '100%' })
        //     .to(".page_title__section--"+(this.nextId+1)+" .page_title__line .char", {
        //         stagger: 0.02,
        //         duration: 0.7,
        //         y: '0',
        //         ease: 'Power4.easeOut'
        //     }, "+=0.2")
        //     .to(".home_pagination_el--"+(this.nextId+1), {
        //         stagger: 0.02,
        //         duration: 0.7,
        //         y: '0',
        //         ease: 'Power4.easeOut'
        //     }, "<0.1")
        // 初次执行动画
        this.tl.set(".page_title__section .page_title__line .char", { y: '100%' })
            .set(".home_pagination_el", { y: '100%' })
            .set(".page_title__section--" + (this.nextId + 1) + " .page_title__line .char", {y: '0'})
            .set(".home_pagination,.frame_logo,.frame_nav,.home_view_project", {opacity: 0,y:"100%"})
            .set(".page_title__group", {opacity: 0})
            .to(".frame_logo,.frame_nav,.home_view_project,.home_pagination", {
                stagger: 0.1,
                duration: 0.7,
                y: '0',
                opacity:1,
                ease: 'Power4.easeOut'
            },">0.5")
            .to(".page_title__group", {
                duration: 0.7,
                opacity:1,
                ease: 'Power4.easeOut'
            },"<0.1")
            .to(".home_pagination_el--" + (this.nextId + 1), {
                stagger: 0.02,
                duration: 0.7,
                y: '0',
                ease: 'Power4.easeOut'
            }, "<0.1")

    }


    //根据位置判断手势方向
    swipeDirection(x1, x2, y1, y2) {
        /**
            * 1. 第一个三元运算符得到如果x轴滑动的距离比y轴大，那么是左右滑动，否则是上下滑动
            * 2. 如果是左右滑动，起点比终点大那么往左滑动
            * 3. 如果是上下滑动，起点比终点大那么往上滑动
            * 需要注意的是这里的坐标和数学中的有些不一定 纵坐标有点反过来的意思
            * 起点p1(1, 0) 终点p2(1, 1)
            */
        return Math.abs(x1 - x2) >=
            Math.abs(y1 - y2) ? (x1 - x2 > 0 ? 'Left' : 'Right') : (y1 - y2 > 0 ? 'Up' : 'Down')
    }


    //绑定滑屏事件
    //手指按下时触发
    startTouch = (e) => {
        this.pageX = e.touches[0].pageX
        this.pageY = e.touches[0].pageY
    }

    //手指移动时触发
    moveTouch = (e) => {
        this.currentX = e.touches[0].pageX
        this.currentY = e.touches[0].pageY
        // this.distance=Math.max(Math.abs(this.currentX-this.pageX),Math.abs(this.currentY-this.pageY))
        // const temp_factor=Math.min(this.distance/100,0.5)
        // gsap.set( this.material.uniforms.dispFactor, {value: temp_factor})
    }

    //手指抬起时触发
    endTouch = (e) => {
        //只有位移大于30才触发 避免了手指点击或者误触的情况
        if ((this.currentX && Math.abs(this.pageX - this.currentX) > 30) ||
            (this.currentY && Math.abs(this.pageY - this.currentY) > 30)) {
            const dir = this.swipeDirection(this.pageX, this.currentX, this.pageY, this.currentY)

            if (dir === "Left" || dir === "Up") {
                this.props.swithslider(true,()=>{
                    this.currId=this.nextId
                    this.nextId=(this.nextId+1)%this.length
                    this.setState({
                        link:portfolioData[this.nextId].link
                    })
                    this.tl.to(".page_title__section--"+(this.currId+1)+" .page_title__line .char", {
                            duration: 0.7,
                            stagger: 0.02,
                            y: '-100%',
                            // yPercent:-100,
                            // ease: 'ease.in'
                            ease: 'Power4.easeIn'
                        })
                        .to(".page_title__section--"+(this.nextId+1)+" .page_title__line .char", {
                            stagger: 0.02,
                            duration: 0.7,
                            y: '0%',
                            startAt:{
                                y:"100%"
                            },
                            ease: 'Power4.easeOut'
                        }, "-=0.3")
                        .to(".home_pagination_el--"+(this.currId+1), {
                            duration: 0.7,
                            y: "-0.18rem",
                            ease: 'Power4.easeIn'
                        }, "<-0.5")
                        .to(".home_pagination_el--"+(this.nextId+1), {
                            duration: 0.7,
                            y: 0,
                            startAt:{
                                y:"100%"
                            },
                            ease: 'Power4.easeOut'
                        },">0")
                        .to(".home_pagination_line_el", {
                            duration: 0.5,
                            x: '100%',
                            ease: 'Power4.easeIn',
                        },"<-1")
                        .to(".home_pagination_line_el", {
                            duration: 0.5,
                            x: '0',
                            startAt:{
                                x:"-100%"
                            },
                            ease: 'Power4.easeOut'},">0.5")

                })
               

            } else {
                this.props.swithslider(false,()=>{
                    this.currId=this.nextId
                    this.nextId=(this.nextId+this.length-1)%this.length
                    this.setState({
                        link:portfolioData[this.nextId].link
                    })
                    this.tl.to(".page_title__section--"+(this.currId+1)+" .page_title__line .char", {
                            duration: 0.7,
                            stagger: 0.02,
                            y: '100%',
                            // yPercent:-100,
                            // ease: 'ease.in'
                            ease: 'Power4.easeIn'
                        })
                        .to(".page_title__section--"+(this.nextId+1)+" .page_title__line .char", {
                            stagger: 0.02,
                            duration: 0.7,
                            y: '0%',
                            startAt:{
                                y:"-100%"
                            },
                            ease: 'Power4.easeOut'
                        }, "-=0.3")
                        .to(".home_pagination_el--"+(this.currId+1), {
                            duration: 0.7,
                            y: "0.18rem",
                            ease: 'Power4.easeIn'
                        }, "<-0.5")
                        .to(".home_pagination_el--"+(this.nextId+1), {
                            duration: 0.7,
                            y: 0,
                            startAt:{
                                y:"-100%"
                            },
                            ease: 'Power4.easeOut'
                        },">0")
                        .to(".home_pagination_line_el", {
                            duration: 0.5,
                            x: '100%',
                            ease: 'Power4.easeIn',
                        },"<-1")
                        .to(".home_pagination_line_el", {
                            duration: 0.5,
                            x: '0',
                            startAt:{
                                x:"-100%"
                            },
                            ease: 'Power4.easeOut'},">0.5")
    
                })
               
            }

            // 重置箭头位置
            this.currentX=0;
            this.currentY=0;
            this.pageX=0;
            this.pageY=0;
        }
    }

    showProject=()=>{
        //当前动画退场
        // this.tl.to(".page_title__section--" + (this.nextId + 1) + " .page_title__line .char", {
        //     stagger: 0.02,
        //     duration: 0.7,
        //     y: '-100%',
        //     ease: 'Power4.easeIn'
        // })
        // .to(".home_view_project,.home_pagination", {
        //     stagger: 0.05,
        //     duration: 0.7,
        //     opacity:0,
        //     ease: 'Power4.easeIn',
        //     onComplete:()=>{
        //         //页面跳转
        //         this.props.history.push(portfolioData[this.nextId].link)
        //     }
        // },"<0.2")
        // ReactDOM.unmountComponentAtNode(document.querySelector(".page"))
        // console.log(this.props)
        //页面跳转
        this.props.history.push(portfolioData[this.nextId].link)
    }

    render() {
        return (
            <div className="page page_cover"  onTouchStart={this.startTouch} onTouchMove={this.moveTouch} onTouchEnd={this.endTouch}>
                <div className="page_title">
                    <div className="page_title__group">
                        {
                            portfolioData && portfolioData.map((item,index)=>{
                                return <div className={`page_title__section page_title__section--${index+1}`} key={item.id}>
                                    <h2 className="page_title__line" data-splitting>{item.title}</h2>
                                </div>
                            })
                        }
                    </div>
                </div>
                <div className="home_pagination">
                    <div className="home_pagination_current">
                        <div className="home_pagination__wrap">
                            {
                                portfolioData && portfolioData.map((item,index)=>{
                                    return <div className={`home_pagination_el home_pagination_el--${index+1}`} key={`h_${item.id}`}>{index+1}</div>
                                })
                            }
                            
                        </div>
                    </div>
                    <div className="home_pagination_line">
                        <div className="home_pagination_line_el"></div>
                    </div>
                    <div className="home_pagination_total">{portfolioData.length}</div>
                </div>
                {/* { this.state.jumpActive && (<Navigate to={portfolioData[this.currId].link} replace='true' />)} */}
                <div className="home_view_project">
                    <div className="home_view_project__con" onClick={this.showProject}>VIEW PROJECT<svg className="home_view_project__arrow"><use xlinkHref="#icon_more"></use></svg></div>
                </div>
                <div className="process_round">
                    <svg viewBox="0 0 54 54" className='process_round__svg'><path className="process_round__path_bg" d="M27,2A25,25,0,1,1,2,27,25,25,0,0,1,27,2"></path><path className="process_round__path" d="M27,2A25,25,0,1,1,2,27,25,25,0,0,1,27,2"></path>
                    </svg>
                </div>
            </div>
        )
    }
}

export default withRouter(SliderDom)