import React, { Component } from 'react'
import gsap from 'gsap'
import imagesLoaded from 'imagesloaded'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"
import { withRouter } from 'react-router-dom'
import Scrollbar from 'smooth-scrollbar'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import ScrollItem from '../../components/ScrollItem'
import SequenceFrame from "../../utils/SeqFrame"
import MuxFrameAnimation from "../../utils/_muxFrameAnimation"
import "video-react/dist/video-react.css"

function PrefixInteger(num, n) {
  return (Array(n).join(0) + num).slice(-n);
}

class Inventory extends Component {

  scrollRef = React.createRef()

  componentDidMount() {
    Splitting({ target: document.querySelectorAll(".project_start_title__con"), by: 'chars' })
    this.isNight = false
    this.isAnimating = false
    this.tl = gsap.timeline()
    this.tl.set(".project_start_title__con .char,.project_scroll__line_el", { y: '100%' })
      .set(".project_scroll__text", { opacity: 0, y: "100%" })
      .to(".project_start_title__con .char", {
        stagger: 0.02,
        duration: 0.7,
        y: '0',
        ease: 'Power4.easeOut'
      }, ">0.5")
      .to(".project_scroll__text", {
        duration: 0.7,
        y: '0',
        opacity: 1,
        ease: 'Power4.easeOut'
      }, ">0")
      .to(".project_scroll__line_el", {
        duration: 0.5,
        y: '0',
        ease: 'Power4.easeOut'
      }, "<0.5")

    //初始化滚动条
    this.initScroll()
    this.anime()

  }

  initScroll() {
    //使用滚动条回弹效果
    Scrollbar.use(OverscrollPlugin)
    this.scrollDom = this.scrollRef.current

    //实例化滚动
    this.Scroll = Scrollbar.init(this.scrollDom, {
      continuousScrolling: false,
      damping: 0.05
    })

    this.Scroll.updatePluginOptions('overscroll', {
      effect: 'glow',
      glowColor: "rgba(255,255,255,.3)",
      maxOverscroll: 30
    })

    this.Scroll.track.xAxis.element.remove()
    this.Scroll.track.yAxis.element.remove()

    //去除默认滚动条样式
    Scrollbar.detachStyle()

    //图片预加载获取高度
    this.imgLoad = imagesLoaded(document.querySelector(".project_page--inventory"))
    this.imgLoad.on('progress', (instance, image) => {
      image.img.style.height = image.img.height + "px"
    })
    this.imgLoad.on('always', (instance) => {
      this.addScrollItems()
    })

  }

  anime() {
    
    //添加动画节点
    this.anim_1 = this.addFrameAni("banner", 22, "jpg", (index, sIndex, allIndex) => {
      //动画局部循环
      if (allIndex == 21) {
        this.anim_1.gotoFrame(11)
      }
    })

    this.anim_2 = this.addFrameAni("star", 25, "jpg", (index, sIndex, allIndex) => {
      if (allIndex == 24) {
        this.anim_2.gotoFrame(10)
      }
    })

    this.anim_3 = this.addFrameAni("element", 19, "png", (index, sIndex, allIndex) => {
      if (allIndex == 18) {
        this.anim_3.gotoFrame(6)
      }
    })

    this.anim_4 = this.addFrameAni("ship", 26, "png")

    this.anim_5 = this.addFrameAni("day", 46, "png", (index, sIndex, allIndex) => {
      if (allIndex == 20) {
        this.anim_5.gotoFrame(5)
      }
    })

    this.anim_6 = this.addFrameAni("submarine", 27, "jpg", (index, sIndex, allIndex) => {
      if (allIndex == 26) {
        this.anim_6.gotoFrame(10)
      }
    })

    //获取动画节点，以便于判断位置控制动画暂停播放提升性能
    this.scroll_item_1 = new ScrollItem(document.querySelector(".anim_pane--banner"), '.anim_pane--banner')
    this.scroll_item_2 = new ScrollItem(document.querySelector(".anim_pane--star"), '.anim_pane--star')
    this.scroll_item_3 = new ScrollItem(document.querySelector(".anim_pane--element"), '.anim_pane--element')
    this.scroll_item_4 = new ScrollItem(document.querySelector(".anim_pane--ship"), '.anim_pane--ship')
    this.scroll_item_5 = new ScrollItem(document.querySelector(".anim_pane--day"), '.anim_pane--day')
    this.scroll_item_6 = new ScrollItem(document.querySelector(".anim_pane--submarine"), '.anim_pane--submarine')

  }

  //根据名称，格式，帧数添加动画 
  addFrameAni = (name, total, format = "png", callback) => {
    var imgsArr = [];
    for (var i = 1; i <= total; i++) {
      let str = '0' + PrefixInteger(i, 2) + '.' + format;
      imgsArr.push(str);
    }
    let anim = new window.MuxFrameAnimation({
      element: '.anim_pane--' + name + ' .anim_pane__canvas',
      frames: imgsArr,
      url: '/dist/images/project/inventory/ani_' + name + '/',
      fps: 16,
      loop: 0,//0 表示循环播放
      autoPlay: true,
      autoSplit: false,
      // useWebGL:true,
      // keep:[
      //   {range:[11,21], loop: 0}
      // ],
      onRenderFrame: (index, sIndex, allIndex) => {
        callback && callback(index, sIndex, allIndex)
      }
    })
    return anim
  }

  //切换白天黑夜
  switchDay = (e) => {
    //判断当前是否有白天切换动画在执行
    if (this.isAnimating) return
    if (this.isNight) {
      e.target.classList.remove("checked")
      document.querySelector(".anim_pane--day").classList.remove("changed")
      this.isNight = false
      this.anim_5.gotoRangeFrame(5, 20)
      // this.ani_day.repeat(6, 21, 1)
      this.isAnimating = true
    } else {
      e.target.classList.add("checked")
      document.querySelector(".anim_pane--day").classList.add("changed")
      // this.ani_day.repeat(28, 42, 1)
      this.anim_5.gotoRangeFrame(27, 41, 0, 1)
      this.isNight = true
      this.isAnimating = true
    }
    //通过回调函数将动画状态重置
    e.target.addEventListener("transitionend", () => {
      this.isAnimating = false
    })
  }

  addScrollItems = () => {

    this.thumb_items = []
    document.querySelectorAll('.layout_picture .scaleup').forEach(item => this.thumb_items.push(new ScrollItem(item, '.layout_thumb_img')))

    this.next_proj = new ScrollItem(document.querySelector(".next_project"), '.next_project__con')
    this.ship_item = new ScrollItem(document.querySelector(".anim_pane--ship"), '.anim_pane__canvas')

    this.Scroll.addListener((s) => { this.onScroll(s) })
    // this.imgLoad.on( 'always', this.onAlways )
  }

  natigateSite = () => {
    this.props.history.push("https://y.qq.com/m/sact/year_personal_2020/dist/index.html?_video=true")
  }

  onScroll({ limit, offset }) {
    this.thumb_items.forEach((item) => {
      if (item.getView()) {
        item.addActiveClass("animated")
      }
    })

    if (this.next_proj.getView()) {
      this.next_proj.addActiveClass("animated")
    }

    if (this.ship_item.getView()) {
      this.ship_item.layoutTranslateY(offset.y, 60, -60)
    }

    // 超出屏幕外的内容暂停动画
    this.checkScrollAni(this.scroll_item_1, this.anim_1)
    this.checkScrollAni(this.scroll_item_2, this.anim_2)
    this.checkScrollAni(this.scroll_item_3, this.anim_3)
    this.checkScrollAni(this.scroll_item_4, this.anim_4)
    this.checkScrollAni(this.scroll_item_5, this.anim_5)
    this.checkScrollAni(this.scroll_item_6, this.anim_6)

  }

  //根据传入的节点控制动画播放
  checkScrollAni = (elem, elem_ani) => {
    if (elem.getView()) {
      if (!elem_ani.isAnimating) {
        elem_ani.isAnimating = true
        elem_ani.play()
      }
    } else {
      if (elem_ani.isAnimating) {
        elem_ani.isAnimating = false
        elem_ani.pause()
      }
    }

  }

  navigateNext = () => {
    this.props.history.push("/portfolio/tmeland")
  }


  render() {
    return (
      <>
        <div className="project_page project_page--inventory" ref={this.scrollRef}>
          <div className="project_page__wrap">
            <div className="page_start">
              <div className="project_start_title">
                <h3 className="project_start_title__con">INVENTORY</h3>
              </div>
            </div>
            <div className="page_content">

              <div className="project_intro">
                <h4 className="project_intro__tit">INVENTORY</h4>
                <div className="project_intro__desc">
                  <p className="project_intro__para">2020年,QQ音乐开创性让报告会唱歌，让人在"歌声"里重温2020，以此诠释更"懂你"的音乐陪伴。</p>
                  <p className="project_intro__para">今年QQ音乐的听歌报告，配合"你的音乐行星"主题，画面采用3D立体星球设计，色彩明快，充满年轻、活力的正能量，尽显高颜值。</p>
                  <p className="project_intro__para">在这个不平凡的2020年末，QQ音乐用一份充满新意的年度听歌报告，将4000万曲库的浩瀚宇宙继续带给每个用户，同时更打开了人与音乐之间的任意门，让我们在回忆里重温有歌陪伴的每个瞬间。</p>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">YEAR</p>
                    <p className="project_intro__data_subtxt">2020</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Technologies</p>
                    <p className="project_intro__data_subtxt">React , Javascript</p>
                  </div>
                </div>
              </div>

              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--banner">
                    <div className="anim_desc">
                      <h3 className="anim_desc_title">支持局部<br />序列帧循环</h3>
                      <p className="anim_desc_text">Local Keyframes Loop</p>
                    </div>
                    <canvas className="anim_pane__canvas" width={750} height={880}></canvas>
                  </div>
                </div>
              </div>

              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--star">
                    <div className="anim_desc">
                      <h3 className="anim_desc_title">自定义<br />速度倍率</h3>
                      <p className="anim_desc_text">Variable Speed Rate</p>
                    </div>
                    <canvas className="anim_pane__canvas" width={750} height={960}></canvas>
                  </div>
                </div>
              </div>

              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--element">
                    <div className="anim_desc">
                      <h3 className="anim_desc_title">自定义<br />帧数模式</h3>
                      <p className="anim_desc_text">支持配置固定帧数模式或可变帧数模式</p>
                    </div>
                    {/* <div className="anim_pane__el"> */}
                    <canvas className="anim_pane__canvas" width={750} height={750}></canvas>
                    {/* </div> */}
                  </div>
                </div>
              </div>

              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--ship">
                    <canvas className="anim_pane__canvas" width={750} height={560}></canvas>
                  </div>
                </div>
              </div>

              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--day">
                    <div className="anim_desc">
                      <h3 className="anim_desc_title">设置序列帧播放方向</h3>
                      <div className="ani_switch">
                        <p className="ani_switch__text">白天</p>
                        <div className="swith_btn" onClick={this.switchDay}></div>
                        <p className="ani_switch__text">黑夜</p>
                      </div>
                    </div>
                    <canvas className="anim_pane__canvas" width={750} height={750}></canvas>
                  </div>
                </div>
              </div>


              <div className="anim_part">
                <div className="anim_layout">
                  <div className="anim_pane anim_pane--submarine">
                    <div className="anim_desc">
                      <h3 className="anim_desc_title">你的音乐行星</h3>
                      <p className="anim_desc_text">Your Planet Of Music</p>
                    </div>
                    <a href="https://y.qq.com/m/sact/year_personal_2020/dist/index.html?_video=true" target="_blank" className="anim_qrcode">
                      <div className="anim_qrcode__text">识别二维码或点击跳转</div>
                      <img src="/dist/images/project/inventory/qrcode.png" alt="" className="anim_qrcode__img" />
                    </a>
                    <canvas className="anim_pane__canvas" width={750} height={750}></canvas>
                  </div>
                </div>
              </div>

              <div className="next_project" onClick={this.navigateNext}>
                <div className="next_project__con">
                  <h3 className="next_project__title">NEXT PROJECT</h3>
                </div>
                <div className="next_project__bg" style={{ backgroundImage: "url(/dist/images/project/tmeland/cover.png)" }}></div>
              </div>

            </div>
          </div>
        </div>
        <div className="project_bg">
          <div className="project_scroll">
            <div className="project_scroll__text">Scroll</div>
            <div className="project_scroll__line">
              <div className="project_scroll__line_el"></div>
            </div>
          </div>
          <img src="/dist/images/project/inventory/cover.jpg" alt="" className="project_bg__img" />
        </div>
      </>
    )
  }
}

export default withRouter(Inventory)
