import { map } from '../../utils/utils'
import ScrollItem from "."



export default class ScrollTileItem extends ScrollItem{
    constructor(el,targetSelector) {
        super(el)
        this.DOM.lines = this.DOM.el.querySelectorAll(targetSelector)
        this.current=0
        this.cache=0
    }

    layoutTranslateX(scrolltop,minValue=-30,maxValue=30) {
        // console.log(scrolltop)
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        Array.from(this.DOM.lines).forEach((item,index)=>{
            const val=index%2===0?this.value:-this.value
            item.style.transform = `translate3d(${val}px,0,0)`
        })
        // console.log("top=" + this.value)
        // translates the image
        
    }

    layoutTranslateY(scrolltop,minValue=-30,maxValue=30) {
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        Array.from(this.DOM.lines).forEach((item,index)=>{
            const val=index%2===0?this.value:-this.value
            item.style.transform = `translate3d(0,${val}px,0)`
        })
    }


    layoutSkewX(scrolltop,minValue=-15,maxValue=15) {
        this.current=scrolltop
        const distance=this.current-this.cache
        this.cache=this.current
        this.skewVal = map(distance, -50, 50, minValue, maxValue)
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, -80, 80)
        Array.from(this.DOM.lines).forEach((item,index)=>{
            const val=index%2===0?this.value:-this.value
            const skewval=index%2===0?this.skewVal:-this.skewVal
            item.style.transform = `translate3d(${val}px,0,0) skewX(${skewval}deg)`
        })
    }

    layoutSkewY(scrolltop,minValue=-15,maxValue=15) {
        this.current=scrolltop
        const distance=this.current-this.cache
        this.cache=this.current
        this.skewVal = map(distance, -50, 50, minValue, maxValue)
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, -80, 80)
        Array.from(this.DOM.lines).forEach((item,index)=>{
            const val=index%2===0?this.value:-this.value
            const skewval=index%2===0?this.skewVal:-this.skewVal
            item.style.transform = `translate3d(0,${val}px,0) skewY(${skewval}deg)`
        })
    }

    layoutSkewXRange(scrolltop,minValue=-15,maxValue=15,range=80) {
        this.current=scrolltop
        const distance=this.current-this.cache
        this.cache=this.current
        this.skewVal = map(distance, -50, 50, minValue, maxValue)
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, range, -range)
        Array.from(this.DOM.lines).forEach((item,index)=>{
            const val=index%2===0?this.value:-this.value
            const skewval=index%2===0?this.skewVal:-this.skewVal
            item.style.transform = `translate3d(${val}px,0,0) skewX(${skewval}deg)`
        })
    }

    layoutScale(scrolltop, minValue = 1, maxValue =1.1) {
        //设置图片最大偏移数量
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        Array.from(this.DOM.lines).forEach((item)=>{
            item.style.transform = `scale(${this.value})`
        })
        // this.DOM.target.style.transform = `scale(${this.value})`
        // this.DOM.target.style.transform = `translate3d(0,${this.value}px,0)`
    }

    //   let scroll = {cache: 0, current: 0};
// const allImgs = [...document.querySelectorAll('.gallery__item-img')];
// lscroll.on('scroll', (obj) => {
//     scroll.current = obj.scroll.x;
//     const distance = scroll.current - scroll.cache;
//     scroll.cache = scroll.current;
//     const skewVal = map(distance, -50, 50, -15, 15);
//     allImgs.forEach(el => el.style.transform = 'skewX('+clamp(skewVal, -15, 15)+'deg)');
// });
// lscroll.update();


    //     let scroll = {cache: 0, current: 0};
    // const allImgs = [...document.querySelectorAll('.gallery__item-img')];
    // lscroll.on('scroll', (obj) => {
    //     scroll.current = obj.scroll.x;
    //     const distance = scroll.current - scroll.cache;
    //     scroll.cache = scroll.current;
    //     const skewVal = map(distance, -50, 50, -15, 15);
    //     allImgs.forEach(el => el.style.transform = 'skewX('+clamp(skewVal, -15, 15)+'deg)');
    // });
    // lscroll.update();
}