import React from 'react'
import * as THREE from 'three'
import vertexSlider from '../../glsl/vertexAbout.glsl'
import fragmentSlider from '../../glsl/fragmentAbout.glsl'
import ThreeObject from '../../components/ThreeObject'

const tileImg = {
  url: "/dist/images/about/logo_about.png",
}

export default class About extends ThreeObject {

  //获取节点
  aboutRef = React.createRef()
  avatarRef = React.createRef()

  //初始化
  componentDidMount() {
    this.init(this.aboutRef)

    //初始化镜头
    this.initCamera()
    //初始化头像rect
    this.avatarRect = this.avatarRef.getBoundingClientRect();
    //新建时钟time
    this.clock = new THREE.Clock();
    //添加纹理
    this.addTexture([tileImg], () => {
      //加载网格，同时将渲染器、着色器添加到网格中
      this.addMesh({
        uniforms: {
          uTexture: { value: this.textures[0] },
          uTime: { value: 0 },
          uProg: { value: 1.0 },
          uIndex: { value: 1.0 },
        },
        //自定义定点着色器
        customVertexShader: vertexSlider,
        customFragmentShader: fragmentSlider,
        planeWidth: this.avatarRect.width,
        planeHeight: this.avatarRect.height
      })
      //获取图片的宽高和位置
      this.updateAvatarBounds()
      //屏幕尺寸调整时处理
      this.resize()
      //更新
      this.update()

    })

    // const text = new AnimatedText3D('Shooting Stars', { color: '#dc2c5a', size: 0.4 });
    // this.scene.add(text)

  }

  initCamera() {
    // this.camera = new THREE.PerspectiveCamera(
    //   45,
    //   window.innerWidth / window.innerHeight,
    //   0.1,
    //   100
    // );
    // this.camera.position.z = 1;

    // this.camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 1000);
    // this.camera.position.z = 2;
    this.camera = new THREE.OrthographicCamera(
      this.width / -2,
      this.width / 2,
      this.height / 2,
      this.height / -2,
      1,
      1000
    )
    this.camera.position.z = 5;
  }

  updateAvatarBounds() {
    //获取头像
    this.avatarMesh = this.scene.children[0]

    this.avatarMesh.position.y = this.height / 2 - this.avatarRect.height / 2 - this.avatarRect.top
    // this.avatarMesh.position.y=-this.avatarRect.top+this.height/2
    console.log("rect=" + this.avatarRect.top)

  }

  //更新时钟实现动画
  updateAvatarTime() {
    let elapsed = this.clock.getElapsedTime();
    this.material.uniforms.uTime.value = elapsed;
  }

  //绑定事件
  bindEvents() {

  }


  //屏幕尺寸变化后更新图像
  resize() {

  }

  //更新画面
  update() {
    //重复执行update
    requestAnimationFrame(this.update.bind(this))
    this.updateAvatarTime()
    //渲染场景和镜头
    this.renderer.render(this.scene, this.camera)
  }

  render() {
    return (
      <div className="about_box">
        <div className="about_wrap">

          <div className="about_avatar">
            <img src="/dist/images/about/logo_about.png" alt="" className="about_avatar__img" ref={c => this.avatarRef = c} />
          </div>

          <div className="project_intro">
            <h4 className="project_intro__tit">About The Website</h4>
            <div className="project_intro__desc">
              <p className="project_intro__para">网站基于react开发，自主设计研发。采用react-router-dom的前端路由技术，webpack工程化打包，适配移动端。</p>
              <p className="project_intro__para">本站采用threejs驱动，简约化扁平设计，实现了视觉差图形色彩特效与交互相结合，表达个人的设计理念风格想法，这是我个人网站设计的一次小尝试，未来也将突破自我，大胆尝试更多不一样的创作。</p>
            </div>
            <div className="project_intro__data">
              <div className="project_intro__data_el">
                <p className="project_intro__data_tit">YEAR</p>
                <p className="project_intro__data_subtxt">2022</p>
              </div>
              <div className="project_intro__data_el">
                <p className="project_intro__data_tit">Technologies</p>
                <p className="project_intro__data_subtxt">React , Three.js , GLSL</p>
              </div>
            </div>
          </div>


          <div className="project_intro">
            <h4 className="project_intro__tit">About Me</h4>
            <div className="project_intro__desc">
              <p className="project_intro__para">我是卢俊鸿，网页设计师，web前端开发方向，专注于界面设计与用户交互体验，目前就职于深圳腾讯音乐娱乐总部，方向，致力于设计独特个性的网站，打造非凡的用户体验。</p>
            </div>
            <div className="project_intro__data">
              <div className="project_intro__data_side">
                <div className="project_intro__data_side_txt">技术领域</div>
              </div>
              <div className="project_intro__data_con">
                <p className="project_intro__data_unit">HTML5</p>
                <p className="project_intro__data_unit">CSS3</p>
                <p className="project_intro__data_unit">SVG</p>
                <p className="project_intro__data_unit">JAVASCRIPT</p>
                <p className="project_intro__data_unit">RESPONSIVE DESIGN</p>
                <p className="project_intro__data_unit">WEBPACK</p>
                <p className="project_intro__data_unit">NPM</p>
                <p className="project_intro__data_unit">REACT</p>
                <p className="project_intro__data_unit">WEBGL</p>
                <p className="project_intro__data_unit">GLSL</p>
              </div>
            </div>
          </div>

        </div>
        <canvas id="about_cavs" className="about_bg" ref={c => this.aboutRef = c}></canvas>
      </div>

    )
  }
}
