import React, { Component } from 'react'
import gsap from 'gsap'
import { withRouter } from 'react-router-dom'
import Scrollbar from 'smooth-scrollbar'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import ScrollItem from '../../components/ScrollItem'
import imagesLoaded from 'imagesloaded'
import { Player } from 'video-react'
import "video-react/dist/video-react.css"

class Videos extends Component {

  scrollRef = React.createRef()

  componentDidMount() {

    //初始化滚动条
    this.initScroll()

  }

  initScroll() {
    //使用滚动条回弹效果
    Scrollbar.use(OverscrollPlugin)
    this.scrollDom = this.scrollRef.current

    //实例化滚动
    this.Scroll = Scrollbar.init(this.scrollDom, {
      continuousScrolling: false,
      damping: 0.05
    })

    this.Scroll.updatePluginOptions('overscroll', {
      effect: 'glow',
      glowColor: "#222a2d",
      maxOverscroll: 30
    })

    this.Scroll.track.xAxis.element.remove()
    this.Scroll.track.yAxis.element.remove()

    //去除默认滚动条样式
    Scrollbar.detachStyle()

    this.video_items = []
    document.querySelectorAll('.page_video').forEach(item => this.video_items.push(new ScrollItem(item, '.video-react')))
    this.intro_items = []
    document.querySelectorAll('.project_intro').forEach(item => this.intro_items.push(new ScrollItem(item, '.project_intro')))
    this.checkPos()

    this.Scroll.addListener((s) => { this.onScroll(s) })

  }

  onScroll({ limit, offset }) {
    this.checkPos()
  }

  checkPos(){
    this.video_items.forEach((item) => {
      if (item.getView()) {
        item.addActiveClass("animated")
      }
    })
    this.intro_items.forEach((item) => {
      if (item.getView()) {
        item.addActiveClass("animated")
      }
    })
  }

  render() {
    return (
      <>
        <div className="project_page project_page--videos" ref={this.scrollRef}>
          <div className="project_page__wrap">

            <div className="page_content">

              <div className="page_video">
                <Player
                  playsInline
                  poster="/dist/videos/qqmusic_2018.png"
                  src="/dist/videos/qqmusic_2018.mp4"
                />
              </div>

              <div className="project_intro">
                <h4 className="project_intro__tit">QQMUSIC 2018</h4>
                <div className="project_intro__desc">
                  <p className="project_intro__para">QQ音乐2018品牌宣传视频，主要围绕,「乐见大牌」 「QQ音乐新版特性」「 品牌LOGO更新 」「大牌明星」四个主题,通过快节奏和动感文字视觉冲击，彰显个性活力。</p>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">YEAR</p>
                    <p className="project_intro__data_subtxt">2018</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Technologies</p>
                    <p className="project_intro__data_subtxt">Adobe Affter Effect</p>
                  </div>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Resolution</p>
                    <p className="project_intro__data_subtxt">1080P</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Duration</p>
                    <p className="project_intro__data_subtxt">02:00</p>
                  </div>
                </div>
              </div>

              <div className="page_video">
                <Player
                  playsInline
                  poster="/dist/videos/qqmusic_9.png"
                  src="/dist/videos/qqmusic_9.mp4"
                />
              </div>

              <div className="project_intro">
                <h4 className="project_intro__tit">QQ音乐9.0全新版本上线</h4>
                <div className="project_intro__desc">
                  <p className="project_intro__para">QQ音乐9.0宣传视频，采用光影与抽象图形渲染，介绍了独家数字专辑艺人，校园音乐大赛，及更新自创品牌栏目，突出年轻活力。</p>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">YEAR</p>
                    <p className="project_intro__data_subtxt">2019</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Technologies</p>
                    <p className="project_intro__data_subtxt">Adobe Affter Effect</p>
                  </div>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Resolution</p>
                    <p className="project_intro__data_subtxt">1080P</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Duration</p>
                    <p className="project_intro__data_subtxt">00:40</p>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </div>
      </>
    )
  }
}

export default withRouter(Videos)
