import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

// 菜单路由表
const linkList = [
    {
        id:1,
        to:"/portfolio",
        elem:"PORTFOLIO"
    },
    {
        id:2,
        to:"/videos",
        elem:"VIDEOS"
    },
    {
        id:3,
        to:"/research",
        elem:"RESEARCH"
    },
    {
        id:4,
        to:"/about",
        elem:"ABOUT"
    }
]

export default class Menu extends Component {

    state = {
        isMenuOpen:false
    }

    /**
     * 
     * @param {*} flag 控制展开收起菜单
     */
    toggleMenu(flag){
        if(flag){
            this.setState({isMenuOpen:true})
        }else{
            this.setState({isMenuOpen:false})
        }
    }

    // 初始化透明度样式
    initFade(tl){
        if(tl){
            tl.set(".menu_item", { 
                opacity: 0
            })
        }
    }

    /**
     * 
     * @param {*} tl 时间线 设置菜单显示
     */
    fadeIn(tl){
        if(tl){
            // const menuItems=li
            tl.to(".menu_item", { 
                duration: 1.1,
                ease: 'power4',
                startAt: {x: 60},
                x: 0,
                opacity: 1,
                stagger: 0.05
            }, '>-=0.2');
        }
    }    
    
    
    /**
     * 
     * @param {*} tl 时间线 设置菜单显示
     */
    fadeOut(tl){
        if(tl){
            // const menuItems=li
            tl.to(".menu_item", { 
                duration: 0.8,
                ease: 'power2.in',
                x: 100,
                opacity: 0,
                stagger: -0.05
            }, 0)
        }
    }

    handleMenu=()=>{
        this.props.handle()
    }

    render() {
        return (
            <section className={`menu_wrap ${this.state.isMenuOpen ? "menu_wrap--open":""}`}>
                <nav className="menu_nav">
                    {
                       linkList && linkList.map((item)=>{
                            return <NavLink className={({isActive})=>isActive?"menu_item current":"menu_item"} key={item.id} to={item.to} onClick={this.handleMenu}>
                                <div className="menu_item__text">{item.elem}</div>
                                <div className="menu_item__id">0{item.id}</div>
                            </NavLink>
                        })
                    }
                </nav>
            </section>
        )
    }
}
