import React, { Component } from 'react'
import GridTransition from '../../components/GridTransition'
import GridDetail from './GridDetail'
import {researchData} from '../../data/researchData'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"

export default class Research extends Component {

    state={
        detailId:"p1"
    }

    detailRef = React.createRef()

    componentDidMount() {
        const gt = new GridTransition({
            el: document.querySelector(".grid")
        })

        // const tl=gsap.timeline()
        // 拆分标题为单个文字()
        Splitting({ target: document.querySelectorAll(".grid_htitle__inner"), by: 'chars' })
        this.isAnimating = false

        //聚焦展示元素
        gt.showGrid(() => {
            this.isAnimating = false
        })

        document.querySelectorAll(".grid_item").forEach(item => {
            item.onclick = () => {
                if (this.isAnimating) return
                // console.log(item)
                // console.log(item.dataset.id)
                this.setState({
                    detailId:item.dataset.id
                })
                gt.showDetail(item, () => {
                    this.isAnimating = false
                    this.detailRef.reloadImage()
                })
            }
            // gt.showGrid()
        })


        document.querySelector(".grid_act_close").onclick = () => {
            if (this.isAnimating) return
            gt.hideDetail(() => {
                this.isAnimating = false
            })
        }

    }
    render() {
        return (
            <div className="grid">
                <div className="grid_detail">
                    <div className="grid_detail__wrap">
                        <div className="grid_detail__hd"></div>
                        <div className="grid_detail__bd">
                            <GridDetail id={this.state.detailId} ref={c => this.detailRef = c}/>
                        </div>

                    </div>
                </div>
                <div className="grid_act">
                    <a href="#" className="grid_act_close">
                        <svg className="grid_act_close__icon"><use xlinkHref="#icon_close"></use></svg>
                    </a>
                </div>
                <div className="grid_main">
                    <div className="grid_main__hd">
                        <h3 className="grid_htitle">
                            <span className="grid_htitle__inner">My Research</span>
                        </h3>
                        <div className="grid_main__hd_line">
                            <div className="grid_main__hd_line_inner"></div>
                        </div>
                    </div>
                    <div className="grid_group">
                        <div className="grid_list">
                            {
                                researchData && researchData.map((item)=>{
                                    return <div className="grid_item" key={item.id} data-id={item.id}>
                                        <div className="grid_item_thumb" style={{ backgroundImage: `url(${item.cover})` }}></div>
                                    </div>
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
