import gsap from 'gsap'
import { Flip } from 'gsap/Flip';
import { map } from '../../utils/utils'
gsap.registerPlugin(Flip);


// Item
export default class GridTransition {
    constructor(option) {
        //初始化DOM
        this.DOM = {}
        //元素根节点
        this.DOM.el = document.querySelector(".grid")
        //展开后内容节点
        this.DOM.content_item = document.querySelector(".grid_detail__hd")
        //目标节点
        this.DOM.target_item = document.querySelector(".grid_item_target .grid_item_thumb")
        //所有的元素节点
        this.DOM.grid_items = document.querySelectorAll(".grid_item")
        //合并参数
        this.DOM = Object.assign(this.DOM, option)
        //获取元素的数量
        this.length = this.DOM.grid_items.length
        //设置内容显示的高度范围，数值为0~1
        this.range = 0.8
        //当前动画状态
        this.isAnimating = false
        //内容视窗的宽高
        this.winWidth = window.innerWidth
        this.winHeight = window.innerHeight
        this.ratio = this.winWidth / 375
        //切片的排列数量与大小
        this.rect = {
            col: 3,
            row: 4,
            size_x: 80 * this.ratio,
            size_y: 80 * this.ratio
        }
        //存放位置的数组
        this.pos = []

        //初始化
        this.init()
        //排列元素
        this.layoutGrid()

    }
    init() {

    }

    layoutGrid = () => {
        //获取排列数
        const { col, row, size_x, size_y } = this.rect
        //计算外边距
        const item_space = this.winWidth / col - size_x
        //模块之间的垂直间隔
        const gap_y = (this.winHeight - size_y * row - item_space * (row - 1)) / 2
        //获取初始位置间隔
        const gap = {
            x: 0,
            // y:(1-this.range)/2*this.winHeight
            y: gap_y
        }
        //取出切片的相关数值
        for (let i = 0; i < row; i++) {
            for (let j = 0; j < col; j++) {
                let pos_x = 0
                if (i % 2 === 0) {
                    //奇数行的最后一个跳过
                    if (j === col - 1) {
                        continue
                    }
                    //奇数行的间隔增加0.5倍
                    pos_x = gap.x + this.winWidth / col * (j + 0.5)
                } else {
                    //偶数行递增
                    pos_x = gap.x + this.winWidth / col * j
                }
                //依次存放位置到数组中
                this.pos.push({
                    x: pos_x,
                    y: gap.y + (size_y + item_space) * i,
                })
            }
        }

        let ran_x = 0
        let ran_y = 0
        // 在初始位置上添加随机偏移
        this.DOM.grid_items.forEach((item, index) => {
            // ran_x=Math.random()*(this.winWidth/col-size_x)
            ran_x = (Math.random() * 0.6 + 0.1) * item_space
            item.style.left = this.pos[index].x + ran_x + "px"
            // ran_y=Math.random()*(this.winHeight/(row+1)-size_y)
            ran_y = Math.random() * item_space * 0.5
            item.style.top = this.pos[index].y + ran_y + "px"
        });


    }

    //显示所有网格排列
    showGrid(callback) {
        //判断当前是否有动画在执行
        if (this.isAnimating) return
        this.isAnimating = true

        //添加类名以便于显示元素
        this.DOM.el.classList.add('grid--open')
        // 初始化timeline
        const tl = gsap.timeline({
            onComplete: () => {
                this.isAnimating = false;
            }
        })
        tl.addLabel('start', 0)
        //重置节点状态
        .set(this.DOM.grid_items, { opacity: 0 })
        .set(".grid_htitle__inner .char", { y: '102%' })
        .set(".grid_main__hd_line_inner", { x: '-102%' })
        .to(".grid_htitle__inner .char",{
            stagger: 0.02,
            duration: 0.5,
            y: '0',
            ease: 'expo.out'
        },"start+=1.2")
        .to(".grid_main__hd_line_inner",{
            duration: 0.4,
            x: '0',
            ease: 'expo.out'
        },"start+=1.3")
        for (let item of this.DOM.grid_items) {
            //获取初始位置
            const { x, y } = this.getStartPos(item)
            //根据距离按比例计算出延迟时间
            const delay = map(this.getDistance(item), 0, 100, 0, 1.8)
            //设置每个元素的初始值
            // tl.set(item, {
            //     x: x,
            //     y: y,
            //     opacity: 0
            // })
            tl.to(item, {
                    duration: 1,
                    ease: "expo.out",
                    opacity: 1,
                    x: 0,
                    y: 0,
                    startAt:{
                        x:x,
                        y:y
                    },
                    delay: delay,
                    onComplete: () => {
                        callback && callback()
                    }
                }, "start+=0.4")
        }
       


    }

    //获取节点的初始位置，默认偏移数量为500
    getStartPos(elem, distance = 500) {
        // 根据当前位置计算元素的中心点坐标
        const elem_center = {
            x: elem.offsetLeft + elem.offsetWidth / 2,
            y: elem.offsetTop + elem.offsetHeight / 2
        }
        //计算出元素中心坐标点与视窗中心点的夹角
        const angle = Math.atan2(Math.abs(this.winHeight / 2 - elem_center.y), Math.abs(this.winWidth / 2 - elem_center.x))
        //计算出应该偏移的垂直和水平距离
        let pos_x = Math.abs(Math.cos(angle) * distance)
        let pos_y = Math.abs(Math.sin(angle) * distance)

        //根据方位判断偏移的方向正负
        return {
            x: elem_center.x > this.winWidth / 2 ? pos_x : -pos_x,
            y: elem_center.y > this.winHeight / 2 ? pos_y : -pos_y
        }
    }

    //获取元素中心坐标点到屏幕视窗中心点的直线距离
    getDistance(elem) {
        const elem_center = {
            x: elem.offsetLeft + elem.offsetWidth / 2,
            y: elem.offsetTop + elem.offsetHeight / 2
        }
        //通过勾股定理的平方根计算距离
        return Math.sqrt(Math.abs(elem_center.x - this.winWidth / 2), Math.abs(elem_center.y - this.winHeight / 2))
    }

    //展开显示内容详情
    showDetail = (targetElem, callback) => {
        //判断当前是否有动画在执行
        if (this.isAnimating) return
        this.isAnimating = true
        //记录目标节点
        this.DOM.target_item = targetElem

        //创建时间线
        const tl = gsap.timeline({
            onComplete: () => {
                this.isAnimating = false
                document.querySelector(".grid_act").classList.add("sho")
                callback && callback()
            }
        })
        //标记开始位置标签
        tl.addLabel('start', 0)
            .set(".grid_detail_subtit,.grid_detail_title,.grid_detail_article", { opacity: 0, y: 30 })
            //展示标题
            .to(".grid_main__hd_line_inner", {
                duration: 0.5,
                x: '102%',
                ease: 'expo.out'
            }, "start")
            //标题底部的线条
            .to(".grid_htitle__inner .char", {
                stagger: 0.02,
                duration: 0.5,
                y: '-102%',
                ease: 'expo.out'
            }, "start")
            //背景颜色变化
            .to(".grid", {
                duration: 1,
                ease: 'expo.inOut',
                backgroundColor: '#fff'
            }, "start")

        /*
        for(const item of this.DOM.grid_items){
            //获取初始位置
            const {x,y}=this.getStartPos(item)
            //根据距离按比例计算出延迟时间
            const delay=map(this.getDistance(item),0,100,0,1.8)
            // console.log(delay)
            // console.log(this.getDistance(item))

            
            tl.to(item,{
                duration:1,
                ease:"expo.inOut",
                opacity:0,
                x:x,
                y:y,
                delay:delay
            })
        
        }

        */

        //展开后详细的文本及图片内容
        tl.to(".grid_detail_subtit,.grid_detail_title,.grid_detail_article", {
            stagger: 0.1,
            duration: 1,
            ease: "expo.out",
            opacity: 1,
            y: 0
        }, "start+=0.7")

        // 图片缩放
        tl.add(() => {
            this.DOM.el.classList.remove('grid--open')
            const targetImage = targetElem.querySelector(".grid_item_thumb")
            const imageState = Flip.getState(targetImage)
            this.DOM.content_item.appendChild(targetImage)
            //重置滚动条位置
            document.querySelector(".grid_detail__wrap").scrollTop = 0
            Flip.from(imageState, {
                duration: 1,
                ease: "expo.inOut"
            });
        }, "start")



    }


    //收起显示内容详情
    hideDetail = (callback) => {
        //判断当前是否有动画在执行
        if (this.isAnimating) return
        this.isAnimating = true

        //初始化时间线
        const tl = gsap.timeline({
            onComplete: () => {
                //更新样式
                this.DOM.el.classList.add('grid--open')
                //重置动画状态
                this.isAnimating = false
                //调用回调函数
                callback && callback()
            }
        })

     
        //标记开始时间标签
        tl.addLabel('start', 0)
            //重置节点状态
            .set(this.DOM.grid_items, { opacity: 0 })
            //将目标节点状态设置回原始值
            .set(this.DOM.target_item, { opacity: 1, x: 0, y: 0 })
            .set(".grid_htitle__inner .char", { y: '102%' })
            .set(".grid_main__hd_line_inner", { x: '-102%' })
            .add(() => {
                this.DOM.el.classList.add('grid--open')
                //将图片放回到列表中
                const image_node = this.DOM.content_item.childNodes[0]
                const imageState = Flip.getState(image_node)
                this.DOM.target_item.appendChild(image_node)
                Flip.from(imageState, {
                    duration: 1,
                    ease: "expo.inOut"
                });
            }, "start+=0.4")
            .to(".grid_detail_subtit,.grid_detail_title,.grid_detail_article", {
                stagger: -0.1,
                duration: 0.3,
                ease: "expo.in",
                opacity: 0,
                y: 20
            }, "start")
             //背景颜色变化
             .to(".grid", {
                duration: 1,
                ease: 'expo.inOut',
                backgroundColor: '#000'
            }, "start")
            .to(".grid_htitle__inner .char",{
                stagger: 0.02,
                duration: 0.5,
                y: '0',
                ease: 'expo.out'
            },"start+=1.2")
            .to(".grid_main__hd_line_inner",{
                duration: 0.4,
                x: '0',
                ease: 'expo.out'
            },"start+=1.3")

        //调用节点展开动画
        for (const item of this.DOM.grid_items) {
            //获取初始位置
            const { x, y } = this.getStartPos(item)
            //根据距离按比例计算出延迟时间
            const delay = map(this.getDistance(item), 0, 100, 0, 1.8)
            if (item.isSameNode(this.DOM.target_item)) {
                continue
            }

            tl.to(item, {
                duration: 1,
                ease: "expo.out",
                opacity: 1,
                x: 0,
                y: 0,
                startAt: {
                    x: x,
                    y: y
                },
                delay: delay
            }, "start+=0.8")

        }


    }

}
