        keepOrder: 0, //结合keep一起用
        url: '',
        fps: 30, //每秒帧数，默认每秒30帧。（液晶屏每秒刷新60次，所以帧数最好是可以整除60的数）
        loop: 1, //默认1次播放，0循环
        autoPlay: false,
        autoSplit: !!window.createImageBitmap, //自动分割成单张，依赖createImageBitmap支持性
        offline: false, //是否进行离屏渲染，提升雪碧图帧的渲染性能
        useWebGL: false, //如果支持webgl默认开启
        color: [],
        fsSource: '', // 自定义片元着色器
        showFace: false, //是否显示第一帧当作封面
        filterBlur: 0, //预模糊
        autoDestroy: false, //动画播放完毕是否自动销毁
        playMode: 0, //初始化播放模式，0 顺序, 1 倒序, 2 来回双向播放
        crossOrigin: undefined, //图片是否跨域
        onRenderStart: function(){}, //序列帧开始渲染前的回调，只执行一次
        onRenderFrameBefore: function(){}, //每渲染一帧前的回调
        onRenderFrame: function(index, spriteIndex, allIndex, state, loopCount, keepCount, keepLoopCount){}, //每渲染一帧后的回调
        onRenderOnce: function(loopCount){}, //渲染一个轮回的回调，loop多次的情况下每一个循环执行一次
        onRenderEnd: function(){}, //渲染结束回调
