import React, { Component } from 'react'
import gsap from 'gsap'
import { portfolioData } from '../../data/portfolioData'
import imagesLoaded from 'imagesloaded'

export default class Loading extends Component {

    state={
        loadCount:0
    }

    loadRef=React.createRef()

    componentDidMount() {

        //输出版权信息
        console.log("%c%s", "background:#000;color:#fff;font-size:12px;padding:10px;", "Created By Leon Lu.");

        //创建空标签
        let fragment = document.createDocumentFragment()
        //添加图片到空标签中
        portfolioData && portfolioData.forEach(item=>{
            let img=new Image()
            img.src=item.url
            fragment.appendChild(img)    
        })
        
        this.dom=document.querySelector(".page_loading__num")
        //加载图片数组
        this.imgLoad = imagesLoaded(fragment)
        //更新加载进度
        this.imgLoad.on('progress', (instance, image) => {
            const currentCount = Math.round(instance.progressedCount * 100 / instance.images.length)
            gsap.to(this.dom,{
                innerText:currentCount,
                //初始值为1
                snap: {
                    innerText: 1
                },
                duration:0.3
            })
        })
        this.imgLoad.on('done', (instance) => {
            this.loadRef.classList.add("loaded")
        })
    }


    render() {
        return (
            <div className="page_loading"  ref={c=>{this.loadRef=c}}>
                {/* <div className="page_loading__text">{this.state.loadCount} %</div> */}
                <div className="page_loading__text"><span className="page_loading__num">0</span>%</div>
            </div>
        )
    }
}
