import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class FrameLogo extends Component {
  render() {
    return (
        <div className="frame_logo">
            <Link to="/">
              <h1 className="frame_title">
                  <svg className="frame_title__logo"><use xlinkHref="#icon_logo"></use></svg>
              </h1>
            </Link>
        </div>
    )
  }
}
