import React, { Component } from 'react'
import SliderCavas from './SliderCanvas'
import SliderDom from './SliderDom'

export default class Slider extends Component {
    // 获取Slider背景画布
    sliderCavasRef = React.createRef()
    switchSlider = (flag,callBack) => {
        //调用画布的switch方法切换
        this.sliderCavasRef.switchBg(flag,callBack)
    }
    render() {
        return (
            <>
                <SliderDom swithslider={this.switchSlider} />
                <SliderCavas ref={c => this.sliderCavasRef = c} />
            </>
        )
    }
}
