import React, { Component } from 'react'
import Overlay from '../../components/Overlay'
import PubSub from 'pubsub-js'
import gsap from 'gsap'
import Menu from '../Menu'
import FrameLogo from '../../components/FrameLogo'
import FrameNav from '../../components/FrameNav'

export default class Frame extends Component {

    state = {
        isOpenMenu : false,
        isAnimating: false
    }

    frameMenuRef=React.createRef()

    componentDidMount(){

        // 初始化
        this.bindEvents()
    }

    // 绑定事件
    bindEvents(){
        // this.$els.frameMenu.addEventListener('click', ()=>{
        //     if(!this.isOpenMenu){
        //         this.openMenu();
        //     }else{
        //         this.closeMenu();
        //     }
        // });
    }

    // 判断进出场，处理点击事件 
    handleMenu=()=>{
        if(!this.state.isOpenMenu){
            this.openMenu();
        }else{
            this.closeMenu();
        }
    }

    //打开菜单
    openMenu(){
        //更新Animation状态
        if ( this.state.isAnimating ) return
        this.setState({
            isAnimating : true
        })

        // 初始化时间线
        let tl=gsap.timeline({
            onComplete: () => this.setState({
                isAnimating : false
            })
        })
        
        // 调用遮罩水平进场动画
        this.OverlayRef.enterInHorizontal(tl,this.enterCallback)

        // 菜单样式初始化
        this.MenuRef.initFade(tl)
        
        // 调用遮罩水平出场动画
        this.OverlayRef.enterOutHorizontal(tl)

        // 调用菜单渐隐进场动画
        this.MenuRef.fadeIn(tl)
    }

    // 进场完后会回调函数
    enterCallback=()=>{
        this.MenuRef.toggleMenu(true)
        this.setState({
            isOpenMenu : true
        })
    }
   
    closeMenu(){
        //更新Animation状态
        if ( this.state.isAnimating ) return
        this.setState({
            isAnimating : true
        })

        // 初始化时间线
        let tl=gsap.timeline({
            onComplete: () => this.setState({
                isAnimating : false
            })
        })
        
        // 调用遮罩水平进场动画
        this.OverlayRef.leaveInHorizontal(tl,this.leaveCallback)

        // 菜单样式初始化
        this.MenuRef.initFade(tl)
        
        // 调用遮罩水平出场动画
        this.OverlayRef.leaveOutHorizontal(tl)

        // 调用菜单渐隐进场动画
        this.MenuRef.fadeIn(tl)
    }

    // 出场完后会回调函数
    leaveCallback=()=>{
        this.MenuRef.toggleMenu(false)
        this.setState({
            isOpenMenu : false
        })
    }
   

    render() {
        return (
            <>
                <div className="frame">
                    <FrameLogo/>
                    <FrameNav handle={this.handleMenu} isopen={this.state.isOpenMenu}/>
                </div>
                {/* <Overlay tl={this.tl} menuBtn={()=>this.frameMenuBtn} ref={c=>this.Overlay=c}/> */}
                <Overlay ref={c=>this.OverlayRef=c}/>
                <Menu handle={this.handleMenu} ref={c=>this.MenuRef=c} />
            </>
        )
    }
}
