import { map } from '../../utils/utils'

const MathUtils = {
    // map number x from range [a, b] to [c, d]
    map: (x, a, b, c, d) => (x - a) * (d - c) / (b - a) + c,
    // linear interpolation
    lerp: (a, b, n) => (1 - n) * a + n * b
};


// Item
export default class ScrollItem {
    constructor(el,targetSelector) {
        // the .item element
        this.DOM = { el: el }
        this.winWidth = window.innerWidth
        this.winHeight = window.innerHeight
        this.DOM.target = this.DOM.el.querySelector(targetSelector)
        this.init()
    }
    init() {
        const docScroll = window.pageYOffset || document.documentElement.scrollTop
        this.rect = this.DOM.el.getBoundingClientRect()
        this.props = {
            height: this.rect.height,
            top: docScroll + this.rect.top
        }
    }
    layoutTranslateX(scrolltop,minValue=this.winWidth,maxValue=-this.winWidth) {
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        this.DOM.target.style.transform = `translate3d(${this.value}px,0,0)`
    }
    layoutTranslateY(scrolltop,minValue=-30,maxValue=30) {
        // console.log("scrolltop="+scrolltop)
        // console.log("scop="+this.props.top)
        //设置图片最大偏移数量
        // console.log("height="+this.props.height)
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        // this.value =Math.max(Math.min(this.value, maxValue), minValue)
        // console.log("t=" + this.props.top)
        // translates the image
        this.DOM.target.style.transform = `translate3d(0,${this.value}px,0)`
    }
    layoutScale(scrolltop, minValue = 1, maxValue =1.1) {
        //设置图片最大偏移数量
        // const minValue = 1.1
        // const maxValue = 1
        this.value = map(this.props.top - scrolltop, this.winHeight, -1 * this.props.height, minValue, maxValue)
        // console.log("t=" + this.props.top)
        // console.log("tossssp=" + this.value)
        // translates the image
        // this.DOM.target.style.transform = `translate3d(0,${this.value}px,0)`
        // console.log(this.DOM.target,this.value)
        // this.DOM.target.style.transform = `scale(${this.value})`
        this.DOM.target.style.transform = `translate3d(0,${this.value}px,0)`
    }
    addActiveClass(classname="active"){
        if(!this.DOM.el.classList.contains(classname)){
            this.DOM.el.classList.add(classname)
        }
    }
    //是否在可视窗口内
    getView() {
        this.rect = this.DOM.el.getBoundingClientRect()
        if (this.rect.top < this.winHeight && this.rect.top > -this.rect.height) {
            return true
        } else {
            return false
        }
    }
}
