import React, { Component } from 'react'
import PropType from 'prop-types'

export default class FrameNav extends Component {

    static propsType={
        isopen : PropType.bool.isRequired
    }

    handleMenu=()=>{
        this.props.handle()
    }

    render() {
        return (
            <div className="frame_nav" onClick={this.handleMenu}>
                <div className={`frame_menu ${this.props.isopen ? "open" : ""}`}>
                    <div className="frame_menu__shape"></div>
                </div>
            </div>
        )
    }
}
