import React, { Component } from 'react'
import gsap from 'gsap'
import imagesLoaded from 'imagesloaded'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"
import { withRouter } from 'react-router-dom'
import Scrollbar from 'smooth-scrollbar'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import ScrollItem from '../../components/ScrollItem'
import { Player } from 'video-react'
import "video-react/dist/video-react.css"

class Hiphop extends Component {

  scrollRef = React.createRef()

  componentDidMount() {
    Splitting({ target: document.querySelectorAll(".project_start_title__con"), by: 'chars' })
    this.tl = gsap.timeline()
    this.tl.set(".project_start_title__con .char,.project_scroll__line_el", { y: '100%' })
      .set(".project_scroll__text", { opacity: 0, y: "100%" })
      .to(".project_start_title__con .char", {
        stagger: 0.02,
        duration: 0.7,
        y: '0',
        ease: 'Power4.easeOut'
      }, ">0.5")
      .to(".project_scroll__text", {
        duration: 0.7,
        y: '0',
        opacity: 1,
        ease: 'Power4.easeOut'
      }, ">0")
      .to(".project_scroll__line_el", {
        duration: 0.5,
        y: '0',
        ease: 'Power4.easeOut'
      }, "<0.5")

    //初始化滚动条
    this.initScroll()

  }

  initScroll() {
    //使用滚动条回弹效果
    Scrollbar.use(OverscrollPlugin)
    this.scrollDom = this.scrollRef.current

    //实例化滚动
    this.Scroll = Scrollbar.init(this.scrollDom, {
      // delegateTo: document,
      continuousScrolling: false,
      // overscrollEffect: 'bounce',
      // overscrollEffect: 'glow',
      damping: 0.05
    })

    this.Scroll.updatePluginOptions('overscroll', {
      effect: 'glow',
      glowColor: "rgba(255,255,255,.3)",
      maxOverscroll: 30
    })

    this.Scroll.track.xAxis.element.remove()
    this.Scroll.track.yAxis.element.remove()

    //去除默认滚动条样式
    Scrollbar.detachStyle()

    this.imgLoad = imagesLoaded(document.querySelector(".project_page--hiphop"))
    this.imgLoad.on('progress', (instance, image) => {
      image.img.style.height = image.img.height + "px"
    })
    this.imgLoad.on('always', (instance) => {
      this.addScrollItems()
    })

  }

  addScrollItems = () => {

    this.thumb_items = []
    document.querySelectorAll('.layout_picture .scaleup').forEach(item => this.thumb_items.push(new ScrollItem(item, '.layout_thumb_img'))) 
    
    this.next_proj = new ScrollItem(document.querySelector(".next_project"), '.next_project__con')

    this.Scroll.addListener((s) => { this.onScroll(s) })
    // this.imgLoad.on( 'always', this.onAlways )
  }

  onScroll({ limit, offset }) {
    this.thumb_items.forEach((item) => {
      if (item.getView()) {
        item.addActiveClass("animated")
      }
    })

    if(this.next_proj.getView()){
      this.next_proj.addActiveClass("animated")
    }

  }

  navigateNext=()=>{
    this.props.history.push("/portfolio/inventory")
  }


  render() {
    return (
      <>
        <div className="project_page project_page--hiphop" ref={this.scrollRef}>
          <div className="project_page__wrap">
            <div className="page_start">
              <div className="project_start_title">
                <h3 className="project_start_title__con">HIPHOP</h3>
              </div>
            </div>
            <div className="page_content">

              <div className="project_intro">
                <h4 className="project_intro__tit">HIPHOP</h4>
                <div className="project_intro__desc">
                  <p className="project_intro__para">我们研究了说唱类相关的属性，探索积累了多种基于说唱的技术应用，结合创意，将音轨与视觉感官传达融合包装，期盼用户在互动中了解说唱音乐，感受自己制作专属beat的乐趣，让项目具备互动性强、新颖、可玩性高的目标属性。</p>
                  <p className="project_intro__para">在项目中，用户根据自己的喜好，发挥主观创意，表达说唱态度，通过调整音乐伴奏和乐器元素、歌手形象进行多样的组合，让音频和视频的结合在屏幕和耳机上直观的表现出来，过一把音乐创作人的瘾，轻松制作千人千面的精美说唱分享卡片，分享传播给好友，刺激引导其参与其中，提升项目的互动性，让音乐品类和品牌达到裂变的传播效果。</p>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">YEAR</p>
                    <p className="project_intro__data_subtxt">2020</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Technologies</p>
                    <p className="project_intro__data_subtxt">React , Javascript , Lottie</p>
                  </div>
                </div>
              </div>

              <div className="project_part">
                <div className="project_part__title">
                  <div className="project_part__title_no">01.</div>
                  <div className="project_part__title_con">Intro Video</div>
                </div>
                <div className="mobile_layout">
                  <Player
                    playsInline
                    poster="/dist/images/project/hiphop/intro_poster.png"
                    src="/dist/images/project/hiphop/intro.mp4"
                  />
                </div>
              </div>


              <div className="project_part">
                <div className="project_part__title">
                  <div className="project_part__title_no">02.</div>
                  <div className="project_part__title_con">Web UI</div>
                </div>
                <div className="layout_picture">
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p1.png" alt="" className="layout_thumb_img" />
                    {/* <div className="layout_thumb_el"></div> */}
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p2.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p3.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p4.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p5.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p6.gif" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p7.gif" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p8.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="slogen_part slogen_part--hiphop">
                    <div className="slogen_part__wrap">
                      <div className="slogen_part__item">HIPHOP</div>
                      <div className="slogen_part__item">HIPHOP</div>
                      <div className="slogen_part__item">HIPHOP</div>
                    </div>
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p9.png" alt="" className="layout_thumb_img" />
                  </div>
                  <div className="layout_thumb scaleup">
                    <img src="/dist/images/project/hiphop/p10.png" alt="" className="layout_thumb_img" />
                  </div>
                </div>
              </div>

              <div className="next_project" onClick={this.navigateNext}>
                <div className="next_project__con">
                  <h3 className="next_project__title">NEXT PROJECT</h3>
                </div>
                <div className="next_project__bg" style={{backgroundImage:"url(/dist/images/project/inventory/cover.jpg)"}}></div>
              </div>

            </div>
          </div>
        </div>
        <div className="project_bg">
          <div className="project_scroll">
            <div className="project_scroll__text">Scroll</div>
            <div className="project_scroll__line">
              <div className="project_scroll__line_el"></div>
            </div>
          </div>
          <img src="/dist/images/project/hiphop/cover.png" alt="" className="project_bg__img" />
        </div>
      </>
    )
  }
}

export default withRouter(Hiphop)
