import React, { Component } from 'react'
import PubSub from 'pubsub-js'
import gsap from 'gsap'

export default class Overlay extends Component {

    

    componentDidMount(){
        PubSub.subscribe("tl",(_,data)=>{
            console.log("tl....",data)
        })
        
        
    }

    overlayPathRef= React.createRef()

    /**
     * 进场动画前半段
     * @param {*} tl Frame.tl 主页面的时间线
     * @param {*} enterComplete 动画完成后的回调函数
     */
    enterInHorizontal(tl,enterComplete){
        let overlayPath=this.overlayPathRef.current
        if(!tl || !overlayPath){ return}

        tl.set(overlayPath, {
            attr: { d: 'M 100 100 H 100 Q 100 50 100 0 H 100 z' }
        })
        .to(overlayPath, { 
            duration: 0.3,
            ease: 'power4.in',
            attr: { d: 'M 100 100 H 50 Q 0 50 50 0 H 100 z' }
        })
        .to(overlayPath, { 
            duration: 0.2,
            ease: 'power2',
            attr: { d: 'M 100 100 H 0 Q 0 50 0 0 H 100 z' },
            onComplete: () => {
                enterComplete && enterComplete()
            }
        })
    }

    /**
     * 进场动画后半段
     * @param {*} tl Frame.tl 主页面的时间线
     * @param {*} leaveComplete 动画完成后的回调函数
     * @returns 
     */
    enterOutHorizontal(tl,enterComplete){
        let overlayPath=this.overlayPathRef.current
        if(!tl || !overlayPath){ return}

        tl.set(overlayPath, {
            attr: { d: 'M 0 100 H 100 Q 100 50 100 0 H 0 z' }
        })
        .to(overlayPath, { 
            duration: 0.3,
            ease: 'power4.in',
            attr: { d: 'M 0 100 H 50 Q 100 50 50 0 H 0 z' }
        })
        .to(overlayPath, { 
            duration: 0.2,
            ease: 'power2',
            attr: { d: 'M 0 100 H 0 Q 0 50 0 0 H 0 z' },
            onComplete: () => {
                enterComplete && enterComplete()
            }
        })
    }

    /**
     * 出场动画前半段
     * @param {*} tl 
     * @param {*} leaveComplete 
     * @returns 
     */
    leaveInHorizontal(tl,leaveComplete){
        let overlayPath=this.overlayPathRef.current
        if(!tl || !overlayPath){ return}
        tl.set(overlayPath, {
            attr: { d: 'M 0 100 H 0 Q 0 50 0 0 H 0 z' }
        })
        .to(overlayPath, { 
            duration: 0.3,
            ease: 'power4.in',
            attr: { d: 'M 0 100 H 50 Q 100 50 50 0 H 0 z' }
        })
        .to(overlayPath, { 
            duration: 0.2,
            ease: 'power2',
            attr: { d: 'M 0 100 H 100 Q 100 50 100 0 H 0 z' },
            onComplete: () => {
                leaveComplete && leaveComplete()
            }
        })
    }

    /**
     * 出场动画后半段
     * @param {*} tl 
     * @param {*} leaveComplete 
     * @returns 
     */
    leaveOutHorizontal(tl,leaveComplete){
        let overlayPath=this.overlayPathRef.current
        if(!tl || !overlayPath){ return}
        tl.set(overlayPath, {
            attr: { d: 'M 100 100 H 0 Q 0 50 0 0 H 100 z' }
        })
        .to(overlayPath, { 
            duration: 0.3,
            ease: 'power4.in',
            attr: { d: 'M 100 100 H 50 Q 0 50 50 0 H 100 z' }
        })
        .to(overlayPath, { 
            duration: 0.2,
            ease: 'power2',
            attr: { d: 'M 100 100 H 100 Q 100 50 100 0 H 100 z' },
            onComplete: () => {
                leaveComplete && leaveComplete()
            }
        })
    }


    render() {
        return (
            <svg className="page_overlay" width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="none">
                <path ref={this.overlayPathRef} className="page_overlay_path" vectorEffect="non-scaling-stroke" d="M 0 100 V 100 Q 50 100 100 100 V 100 z" />
            </svg>
        )
    }
}
