import * as THREE from 'three'
import gsap from 'gsap'
import Scrollbar from 'smooth-scrollbar'
import vertexShader from '../../glsl/vertexShader2.glsl'
import fragmentShader from '../../glsl/fragmentShader.glsl'

export default class Tile {

    constructor(el, scene) {
        this.el = el
        this.scene = scene

        //预览头图
        this.mainImage = this.el.querySelector('.slidershow_img')

        // const {dom,scene,duration}=props

        // this.scene = scene

        // this.$els = {
        //     body: document.body,
        //     el: $el,
        //     link: $el.querySelector('a'),
        //     text: $el.querySelectorAll('.slidershow_tile__title'),
        //     title: $el.querySelector('.tile__title_line').innerText,
        // }

        //动画时间
        this.duration = 0.5
        this.image = null

        this.texture = null
        //尺寸
        this.sizes = new THREE.Vector2(0, 0)
        //位置
        this.offset = new THREE.Vector2(0, 0)

        this.vertexShader = vertexShader
        this.fragmentShader = fragmentShader

        this.clock = new THREE.Clock()
        this.mouse = new THREE.Vector2(0, 0)
        //滑动位置
        this.scroll = 0
        //上一个位置
        this.prevScroll = 0
        //滑动位移差
        this.delta = 0
        //是否已经点击
        this.hasClicked = false
        //是否已经放大展开
        this.isZoomed = false

        //纹理加载器
        this.loader = new THREE.TextureLoader()

        //图片预加载
        // this.preload([this.mainImage.src], () => { this.initTile() })
        this.preloadImage(this.mainImage.src, () => { this.initTile() })

        //获取滚动模块
        this.Scroll = Scrollbar.get(document.querySelector('.scrollarea'))

        this.bindEvent()

    }

    /* 获取图片位置
    --------------------------------------------------------- */
    getBounds() {
        const { width, height, left, top } = this.mainImage.getBoundingClientRect()

        /// 设置图片尺寸
        if (!this.sizes.equals(new THREE.Vector2(width, height))) {
            this.sizes.set(width, height)
        }

        // 更新图片的位置
        if (!this.offset.equals(new THREE.Vector2(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2))) {
            this.offset.set(left - window.innerWidth / 2 + width / 2, -top + window.innerHeight / 2 - height / 2)
        }
    }

    // 初始化画廊
    initTile() {
        //获得纹理
        const texture = this.image
        //更新图片位置
        this.getBounds()
        //设置着色器uniforms
        this.uniforms = {
            uTexture: {
                value: texture
            },
            uOffset: {
                value: new THREE.Vector2(0.0, 0.0)
            },
            uAlpha: {
                value: 1
            }
        }

        //设置几何体为平面
        this.geometry = new THREE.PlaneBufferGeometry(1, 1, 32, 32)

        //设置材质
        this.material = new THREE.ShaderMaterial({
            uniforms: this.uniforms,
            vertexShader: this.vertexShader,
            fragmentShader: this.fragmentShader,
            transparent: true
        })

        //将材质放置到几何体中
        this.mesh = new THREE.Mesh(this.geometry, this.material)

        //更新位置
        this.mesh.position.x = this.offset.x
        this.mesh.position.y = this.offset.y
        //设置尺寸
        this.mesh.scale.set(this.sizes.x, this.sizes.y, 1)
        //添加到场景中
        this.scene.add(this.mesh)
        //隐藏页面加载好的图片
        this.mainImage.classList.add('is-loaded')
    }


    //绑定事件
    bindEvent() {
        //绑定鼠标滑动事件
        // window.addEventListener('touchstart', (e) => { this.onMouseStart(e) })
        window.addEventListener('touchmove', (e) => { this.onMouseMove(e) })
        window.addEventListener('touchend', (e) => { this.onMouseOut(e) })
        this.Scroll.addListener((s) => { this.onScroll(s) })

    }

    onMouseStart(){
        
    }

    onMouseMove(event) {
        if (this.isZoomed || this.hasClicked) return
        const sliderdom = document.querySelector(".slidershow_list")
        if(sliderdom){
            sliderdom.classList.add("moving")
        }

        gsap.to(this.mouse, {
            x: event.clientX,
            y: event.clientY,
            duration: 0.5
        })
    }

    onMouseOut(event){
        const sliderdom = document.querySelector(".slidershow_list")
        if(sliderdom){
            sliderdom.classList.remove("moving")
        }
    }

    // 数据化滑动速度
    onScroll({ offset, limit }) {
        this.scroll = offset.x / limit.x
    }

    /* 预加载图片 加载完成后删除img的dom节点
    --------------------------------------------------------- */
    preload(imageArr, allImagesLoadedCallback) {
        let loadedCounter = 0
        const toBeLoadedNumber = imageArr.length
        // const preloadImage = (imgItem, anImageLoadedCallback) => {
        //     const image = this.loader.load(imgItem, anImageLoadedCallback)
        //     image.center.set(0.5, 0.5)
        //     this.image = image
        // }

        imageArr.forEach((imgItem) => {
            this.preloadImage(imgItem, () => {
                loadedCounter += 1
                if (loadedCounter === toBeLoadedNumber) {
                    allImagesLoadedCallback()
                }
            })
        })
    }

    preloadImage(imageItem,loadedCallback){
        const image=this.loader.load(imageItem, ()=>{
            loadedCallback && loadedCallback()
        })
        image.center.set(0.5, 0.5)
        image.minFilter = THREE.LinearFilter
        this.image = image
    }

    //移动更新画廊
    move() {
        if (!this.mesh || this.isZoomed || this.hasClicked) return
        this.getBounds()

        gsap.to(this.mesh.position, {
            x: this.offset.x,
            y: this.offset.y,
            ease: 'power4.out',
            duration:0.5, 
            onUpdate: () => {
                let pos = new THREE.Vector3(this.offset.x, this.offset.y, 0);
                let offset = this.mesh.position
                    .clone()
                    .sub(pos)
                    .multiplyScalar(-0.001)
                // console.log(offset)
                // console.log(this.mesh.position)
                this.uniforms.uOffset.value = offset
            }
        })


        gsap.to(this.mesh.scale,  {
            x: this.sizes.x - this.delta,
            y: this.sizes.y - this.delta,
            z: 1,
            duration:0.3
        })

    }

    //更新画廊
    update() {
        this.delta = Math.abs((this.scroll - this.prevScroll) * 2000)

        if (!this.mesh) return

        this.move()

        this.prevScroll = this.scroll

        if (!this.isHovering) return
    }
}
