import React, { Component } from 'react'
import * as THREE from 'three'
import Tile from '../../components/Tile'
import { fanliveData } from '../../data/fanliveData'


export default class Scene extends Component {

    state = { //初始化状态
		canvasTiles:[], //tile列表数组
	} 

    componentDidMount(){
        this.container = this.sceneRef
        this.W = window.innerWidth
        this.H = window.innerHeight

        this.mouse = new THREE.Vector2(0, 0)
        this.activeTile = null
        this.perspective = 800

        this.start()

        this.bindEvent()
    }

    bindEvent() {
        // document.addEventListener('toggleDetail', ({ detail: shouldOpen }) => { this.onToggleView(shouldOpen) })

        window.addEventListener('resize', () => { this.onResize() })
    }


    start() {
        this.mainScene = new THREE.Scene()
        this.initCamera()
        this.initLights()

        this.renderer = new THREE.WebGLRenderer({
            canvas: this.container,
            alpha: true,
        })
        this.renderer.setSize(this.W, this.H)
        this.renderer.setPixelRatio(window.devicePixelRatio)

        

        // this.tiles = Array.from(this.state.tileDoms).map(($el, i) => new Tile())
        // this.tiles = Array.from(this.$tiles).map(($el, i) => {
        //     // console.log($el,i)
        //     return new Tile($el, this, durations[i], shaders[i])
        // })   
        
        const canvasTilesArr=fanliveData && fanliveData.map((item) => {
            const el=document.getElementById(item.id)
            return new Tile(el,this.mainScene)
        })
        this.setState({
            canvasTiles:canvasTilesArr
        })

        this.update()
    }

    initCamera() {
        const fov = (180 * (2 * Math.atan(this.H / 2 / this.perspective))) / Math.PI

        this.camera = new THREE.PerspectiveCamera(fov, this.W / this.H, 1, 10000)
        this.camera.position.set(0, 0, this.perspective)
    }

    initLights() {
        const ambientlight = new THREE.AmbientLight(0xffffff, 2)
        this.mainScene.add(ambientlight)
    }




    /* Handlers
    --------------------------------------------------------- */

    onResize() {
        this.W = window.innerWidth
        this.H = window.innerHeight

        this.camera.aspect = this.W / this.H
        console.log("sssresize")

        this.camera.updateProjectionMatrix()
        this.renderer.setSize(this.W, this.H)
        this.renderer.render(this.mainScene, this.camera)
        this.state.canvasTiles.forEach((tile) => {
            tile.update()
        })

    }

    // onToggleView({ target, open }) {
    //     this.activeTile = target // !== undefined ? target : this.activeTile

    //     // ev('lockScroll', { lock: open })
    //     // ev('tile:zoom', { tile: this.activeTile, open })
    // }

    /* Actions
    --------------------------------------------------------- */

    update() {
        requestAnimationFrame(this.update.bind(this))

        this.state.canvasTiles.forEach((tile) => {
            // console.log(tile)
            tile.update()
        })

        this.renderer.render(this.mainScene, this.camera)
    }


    render() {
        return (
            <canvas id="scene" className="scene" ref={c=>{this.sceneRef=c}}></canvas>
        )
    }
}
