import React, { Component } from 'react'
import Scrollbar from 'smooth-scrollbar'
import gsap from 'gsap'
import { withRouter } from 'react-router-dom'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import TileDom from '../../components/TileDom'
import HorizontalScrollPlugin from '../../utils/HorizontalScrollPlugin'
import { map } from '../../utils/utils'
import { fanliveData } from '../../data/fanliveData'

class Stage extends Component {

    constructor(props) {
        super(props)
        this.progress = 0

        this.tileElement = fanliveData && fanliveData.map((item, index) => {
            // let tileItem=<TileDom title={item.title} url={item.url}/>
            return <li className="slidershow_item" key={item.id}>
                <TileDom {...item} index={index + 1} />
            </li>
        })


    }

    scrollRef = React.createRef()

    componentDidMount() {
        Scrollbar.use(HorizontalScrollPlugin, OverscrollPlugin)

        this.scrollDom = this.scrollRef.current

        // 初始化
        this.init()

        this.bindEvents()
    }

    init() {
        //实例化滚动
        this.Scroll = Scrollbar.init(this.scrollDom, {
            // delegateTo: document,
            continuousScrolling: false,
            overscrollEffect: 'bounce',
            damping: 0.05,
            plugins: {
                horizontalScroll: {
                    events: [/wheel/],
                },
            },
        })

        this.Scroll.track.xAxis.element.remove()
        this.Scroll.track.yAxis.element.remove()

        //去除默认滚动条样式
        Scrollbar.detachStyle()

        //更新自定义滚动条进度
        this.updateScrollBar()

    }

    bindEvents() {
        // document.addEventListener('lockScroll', ({ detail }) => { this.lockScroll(detail) })

        this.Scroll.addListener((s) => { this.onScroll(s) })
    }

    updateScrollBar() {
        const progress = map(this.progress * 100, 0, 100, 5, 100)
        gsap.to(".slideshow_progress__inner", { xPercent: progress, force3D: true, duration: 0.3 })
    }

    onScroll({ limit, offset }) {
        this.progress = offset.x / limit.x


        // gsap.to(this.$els.title, 0.3, { x: -this.progress * offsetTitle, force3D: true })
        this.updateScrollBar()
    }

    navigateNext=()=>{
        this.props.history.push("/portfolio/hiphop")
    }

    render() {
        return (
            <section className="page_slidershow">

                <section ref={this.scrollRef} className="scrollarea slidershow">
                    <ul className="slidershow_list">
                        {this.tileElement}
                        <li className="slidershow_item slidershow_item--sp" onClick={this.navigateNext}>
                            <article className="slidershow_tile">
                                <div className="slidershow_link">
                                    <figure className="slidershow_fig">
                                        <img src="/dist/images/project/hiphop/cover.png" alt="" className="slidershow_img" />
                                    </figure>
                                    <div className="slidershow_np">Next Project<svg className="slidershow_np__arrow"><use xlinkHref="#icon_more"></use></svg></div>
                                </div>
                            </article>
                        </li>
                    </ul>
                </section>
                    <div className="slideshow_progress">
                        <div className="slideshow_progress__inner"></div>
                    </div>
            </section>
        )
    }
}
export default withRouter(Stage)
