import React, { Component } from 'react'
import gsap from 'gsap'
import imagesLoaded from 'imagesloaded'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"
import { withRouter } from 'react-router-dom'
import Scrollbar from 'smooth-scrollbar'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import ScrollItem from '../../components/ScrollItem'
import ScrollTileItem from '../../components/ScrollItem/ScrollTileItem'

class Qqmusic extends Component {

  scrollRef = React.createRef()

  componentDidMount() {
    Splitting({ target: document.querySelectorAll(".project_start_title__con"), by: 'chars' })
    this.tl = gsap.timeline()
    this.tl.set(".project_start_title__con .char,.project_scroll__line_el", { y: '100%' })
      .set(".project_scroll__text", { opacity: 0, y: "100%" })
      .to(".project_start_title__con .char", {
        stagger: 0.02,
        duration: 0.7,
        y: '0',
        ease: 'Power4.easeOut'
      }, ">0.5")
      .to(".project_scroll__text", {
        duration: 0.7,
        y: '0',
        opacity: 1,
        ease: 'Power4.easeOut'
      }, ">0")
      .to(".project_scroll__line_el", {
        duration: 0.5,
        y: '0',
        ease: 'Power4.easeOut'
      }, "<0.5")

    //初始化滚动条
    this.initScroll()

  }

  initScroll() {
    //使用滚动条回弹效果
    Scrollbar.use(OverscrollPlugin)
    this.scrollDom = this.scrollRef.current

    //实例化滚动
    this.Scroll = Scrollbar.init(this.scrollDom, {
      // delegateTo: document,
      continuousScrolling: false,
      // overscrollEffect: 'bounce',
      // overscrollEffect: 'glow',
      damping: 0.05
    })

    this.Scroll.updatePluginOptions('overscroll', {
      effect: 'glow',
      glowColor: "rgba(255,255,255,.3)",
      maxOverscroll: 30
    })

    this.Scroll.track.xAxis.element.remove()
    this.Scroll.track.yAxis.element.remove()

    //去除默认滚动条样式
    Scrollbar.detachStyle()

    this.imgLoad = imagesLoaded(document.querySelector(".project_page--qqmusic"))
    this.imgLoad.on('progress', (instance, image) => {
      image.img.style.height = image.img.height + "px"
    })
    this.imgLoad.on('done', (instance) => {
      this.addScrollItems()
    })

  }

  addScrollItems = () => {
    this.thumb_items = []
    document.querySelectorAll('.case_item_preview').forEach(item => this.thumb_items.push(new ScrollItem(item, '.case_item_preview__img')))
    // console.log(this.thumb_items)

    this.text_items = []
    document.querySelectorAll('.project_view').forEach(item => this.text_items.push(new ScrollTileItem(item, '.project_view__bg')))

    this.spark_items = []
    document.querySelectorAll('.project_spark').forEach(item => this.spark_items.push(new ScrollTileItem(item, '.project_spark__img')))

    this.next_proj = new ScrollItem(document.querySelector(".next_project"), '.next_project__con')

    this.Scroll.addListener((s) => { this.onScroll(s) })
    // this.imgLoad.on( 'always', this.onAlways )
  }

  onScroll({ limit, offset }) {

    this.thumb_items.forEach((item) => {
      if (item.getView()) {
        // item.layoutTranslateY(offset.y, 30, -30)
        item.addActiveClass("animated")
      }
    })

    for (const item of this.text_items) {
      if (item.getView()) {
        item.layoutSkewXRange(offset.y, -20, 20, 250)
      }
    }

    for (const item of this.spark_items) {
      if (item.getView()) {
        item.layoutScale(offset.y, 1.2, 1)
      }
    }

    if (this.next_proj.getView()) {
      this.next_proj.addActiveClass("animated")
    }

  }

  navigateNext = () => {
    this.props.history.push("/portfolio/fanlive")
  }

  render() {
    return (
      <>
        <div className="project_page project_page--qqmusic" ref={this.scrollRef}>
          <div className="project_page__wrap">
            <div className="page_start">
              <div className="project_start_title">
                <h3 className="project_start_title__con">QQMUSIC</h3>
              </div>
            </div>
            <div className="page_content">

              <div className="project_intro">
                <h4 className="project_intro__tit">QQMUSIC</h4>
                <div className="project_intro__desc">
                  <p className="project_intro__para">QQ音乐音乐客户端负责项目，主要围绕「音乐巅峰榜」 「超级订阅」「积分推歌 」「扑通社区」「超级会员VIP 」「Moo」6个相关项目进行汇总。</p>
                  <p className="project_intro__para">客户端二级页面部分采用h5开发，底层基于B/S架构，极大的缩短了开发周期，实现快速更新迭代。</p>
                </div>
                <div className="project_intro__data">
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">YEAR</p>
                    <p className="project_intro__data_subtxt">2020-2022</p>
                  </div>
                  <div className="project_intro__data_el">
                    <p className="project_intro__data_tit">Technologies</p>
                    <p className="project_intro__data_subtxt">H5, CSS3 , Javascript</p>
                  </div>
                </div>
              </div>

              <div className="project_part">
                {/* <div className="project_part__title">
                  <div className="project_part__title_no">01.</div>
                  <div className="project_part__title_con">音乐巅峰榜</div>
                </div> */}
                <div className="case_box">
                  <ul className="case_list">
                    <li className="case_item">
                      <div className="case_item__desc">
                        <p className="case_item_no">01.</p>
                        <p className="case_item_name">音乐巅峰榜</p>
                      </div>
                      <div className="case_item_preview scaleup">
                        <img src="/dist/images/project/qqmusic/p1.png" alt="" className="case_item_preview__img" />
                      </div>
                    </li>
                    <li className="case_item">
                      <div className="case_item__desc">
                        <p className="case_item_no">02.</p>
                        <p className="case_item_name">超级订阅</p>
                      </div>
                      <div className="case_item_preview scaleup">
                        <img src="/dist/images/project/qqmusic/p2.png" alt="" className="case_item_preview__img" />
                      </div>
                    </li>
                    <li className="case_item">
                      <div className="case_item__desc">
                        <p className="case_item_no">03.</p>
                        <p className="case_item_name">积分推歌</p>
                      </div>
                      <div className="case_item_preview scaleup">
                        <img src="/dist/images/project/qqmusic/p3.png" alt="" className="case_item_preview__img" />
                      </div>
                    </li>
                    <li className="case_item">
                      <div className="case_item__desc">
                        <p className="case_item_no">04.</p>
                        <p className="case_item_name">扑通社区</p>
                      </div>
                      <div className="case_item_preview scaleup">
                        <img src="/dist/images/project/qqmusic/p4.png" alt="" className="case_item_preview__img" />
                      </div>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="project_view">
                <img src="/dist/images/project/qqmusic/p5.png" alt="" className="project_view__img" />
                <div className="project_view__bg">
                  <svg className="project_view__bg_txt"><use xlinkHref="#text_svip"></use></svg>
                  <div className="proect_view__bg_con">
                    <div className="proect_view__bg_no">05.</div>
                    <div className="proect_view__bg_name">超级会员</div>
                  </div>
                </div>
              </div>

              <div className="project_spark">
                <div className="project_spark__con">
                  <p className="project_spark_no">06.</p>
                  <p className="project_spark_name">Moo</p>
                </div>
                <div className="project_spark__view">
                  <div className="project_spark__view_el project_spark__view_el--1">
                    <img src="/dist/images/project/qqmusic/p6_1.png" alt="" className="project_spark__img" />
                  </div>
                  <div className="project_spark__view_el project_spark__view_el--2">
                    <img src="/dist/images/project/qqmusic/p6_2.png" alt="" className="project_spark__img" />
                  </div>
                  <div className="project_spark__view_el project_spark__view_el--3">
                    <img src="/dist/images/project/qqmusic/p6_3.png" alt="" className="project_spark__img" />
                  </div>
                  <div className="project_spark__view_el project_spark__view_el--4">
                    <img src="/dist/images/project/qqmusic/p6_4.png" alt="" className="project_spark__img" />
                  </div>
                  <div className="project_spark__view_el project_spark__view_el--5">
                    <img src="/dist/images/project/qqmusic/p6_5.png" alt="" className="project_spark__img" />
                  </div>
                </div>
              </div>

              <div className="slogen_part">
                <div className="slogen_part__wrap">
                  <div className="slogen_part__item">QQMUSIC</div>
                  <div className="slogen_part__item">QQMUSIC</div>
                  <div className="slogen_part__item">QQMUSIC</div>
                </div>
              </div>

              <div className="next_project" onClick={this.navigateNext}>
                <div className="next_project__con">
                  <h3 className="next_project__title">NEXT PROJECT</h3>
                </div>
                <div className="next_project__bg" style={{ backgroundImage: "url(/dist/images/project/fanlive/cover.png)" }}></div>
              </div>

            </div>
          </div>
        </div>
        <div className="project_bg">
          <div className="project_scroll">
            <div className="project_scroll__text">Scroll</div>
            <div className="project_scroll__line">
              <div className="project_scroll__line_el"></div>
            </div>
          </div>
          <img src="/dist/images/project/qqmusic/cover.png" alt="" className="project_bg__img" />
        </div>
      </>
    )
  }
}

export default withRouter(Qqmusic)
