import React, { Component } from 'react'
import gsap from 'gsap'
import "splitting/dist/splitting.css"
import "splitting/dist/splitting-cells.css"
import Splitting from "splitting"
import { withRouter } from 'react-router-dom'
import Scrollbar from 'smooth-scrollbar'
import OverscrollPlugin from 'smooth-scrollbar/plugins/overscroll'
import ScrollItem from '../../components/ScrollItem'
import ScrollTileItem from '../../components/ScrollItem/ScrollTileItem'
import imagesLoaded from 'imagesloaded'
import { Player } from 'video-react'
import "video-react/dist/video-react.css"

class Tmeland extends Component {

    scrollRef = React.createRef()

    componentDidMount() {
        Splitting({ target: document.querySelectorAll(".project_start_title__con"), by: 'chars' })
        this.tl = gsap.timeline()
        this.tl.set(".project_start_title__con .char,.project_scroll__line_el", { y: '100%' })
            .set(".project_scroll__text", { opacity: 0, y: "100%" })
            .to(".project_start_title__con .char", {
                stagger: 0.02,
                duration: 0.7,
                y: '0',
                ease: 'Power4.easeOut'
            }, ">0.5")
            .to(".project_scroll__text", {
                duration: 0.7,
                y: '0',
                opacity: 1,
                ease: 'Power4.easeOut'
            }, ">0")
            .to(".project_scroll__line_el", {
                duration: 0.5,
                y: '0',
                ease: 'Power4.easeOut'
            }, "<0.5")

        //初始化滚动条
        this.initScroll()

    }

    initScroll() {
        //使用滚动条回弹效果
        Scrollbar.use(OverscrollPlugin)
        this.scrollDom = this.scrollRef.current

        //实例化滚动
        this.Scroll = Scrollbar.init(this.scrollDom, {
            continuousScrolling: false,
            damping: 0.05
        })

        this.Scroll.updatePluginOptions('overscroll', {
            effect: 'glow',
            glowColor: "rgba(255,255,255,.3)",
            maxOverscroll: 30
        })

        this.Scroll.track.xAxis.element.remove()
        this.Scroll.track.yAxis.element.remove()

        //去除默认滚动条样式
        Scrollbar.detachStyle()

        this.imgLoad = imagesLoaded(document.querySelector(".project_page--tmeland"))
        this.imgLoad.on('progress', (instance, image) => {
            image.img.style.height = image.img.height + "px"
        })
        this.imgLoad.on('done', (instance) => {
            this.addScrollItems()
        })


    }

    addScrollItems = () => {
        this.items = []
        document.querySelectorAll('.content .item__img-wrap').forEach(item => this.items.push(new ScrollItem(item, '.item__img')))
        this.brand_item = new ScrollItem(document.querySelector(".brand_part"), '.brand_part__logo')
        this.thumb_items = []
        document.querySelectorAll('.layout_picture .scaleup').forEach(item => this.thumb_items.push(new ScrollItem(item, '.layout_thumb_img')))
        this.Scroll.addListener((s) => { this.onScroll(s) })
        this.columns_items = []
        document.querySelectorAll('.tiles--columns').forEach(item => this.columns_items.push(new ScrollTileItem(item, '.tiles_line')))

        this.next_proj = new ScrollItem(document.querySelector(".next_project"), '.next_project__con')

    }

    onScroll({ limit, offset }) {
        // console.log(limit, offset)
        this.items.forEach((item) => {
            // console.log(item.getView())
            if (item.getView()) {
                item.layoutTranslateX(offset.y)
            }
        })
        this.thumb_items.forEach((item) => {
            if (item.getView()) {
                item.addActiveClass("animated")
            }
        })
        if (this.brand_item.getView()) {
            // this.brand_item.addActiveClass("animated")
            this.brand_item.layoutTranslateX(offset.y)
        }
        for (const item of this.columns_items) {
            // console.log(item.getView())
            if (item.getView()) {
                item.layoutTranslateY(offset.y, -100, 100)
            }
        }
        if (this.next_proj.getView()) {
            this.next_proj.addActiveClass("animated")
        }

    }

    navigateNext = () => {
        this.props.history.push("/portfolio/qqmusic")
    }


    render() {
        return (
            <>
                <div className="project_page project_page--tmeland" ref={this.scrollRef}>
                    <div className="project_page__wrap">
                        <div className="page_start">
                            <div className="project_start_title">
                                <h3 className="project_start_title__con">TMELAND</h3>
                            </div>
                        </div>
                        <div className="page_content">

                            <div className="project_intro">
                                <h4 className="project_intro__tit">TMELAND</h4>
                                <div className="project_intro__desc">
                                    <p className="project_intro__para">全新虚拟音乐嘉年华TMELAND入口已开启，神奇数字时空任你穿梭，享受独一无二的虚拟音乐新纪元。</p>
                                    <p className="project_intro__para">虚拟与现实结合的沉浸式体验，在TMELAND发现音乐新玩法，DIY你的专属数字岛屿，随时随地“听我想听”。</p>
                                    <p className="project_intro__para">脑洞大开的黑科技，将想象中的精彩逐一实现，让音乐的能量穿透时空，惊喜的数字磁场，共振万千乐迷的心。</p>
                                </div>
                                <div className="project_intro__data">
                                    <div className="project_intro__data_el">
                                        <p className="project_intro__data_tit">YEAR</p>
                                        <p className="project_intro__data_subtxt">2021</p>
                                    </div>
                                    <div className="project_intro__data_el">
                                        <p className="project_intro__data_tit">Technologies</p>
                                        <p className="project_intro__data_subtxt">React , Javascript</p>
                                    </div>
                                </div>
                            </div>

                            <div className="project_part">
                                <div className="project_part__title">
                                    <div className="project_part__title_no">01.</div>
                                    <div className="project_part__title_con">Intro Video</div>
                                </div>
                                <div className="mobile_layout">
                                    <Player
                                        playsInline
                                        poster="/dist/images/project/tmeland/intro_poster.png"
                                        src="/dist/images/project/tmeland/intro.mp4"
                                    />
                                </div>
                            </div>

                            <div className="project_part">
                                <div className="project_part__title">
                                    <div className="project_part__title_no">02.</div>
                                    <div className="project_part__title_con">Mobile</div>
                                </div>
                                <div className="layout_picture">
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p1.png" alt="" className="layout_thumb_img" />
                                        {/* <div className="layout_thumb_el"></div> */}
                                    </div>
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p2.png" alt="" className="layout_thumb_img" />
                                    </div>
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p3.png" alt="" className="layout_thumb_img" />
                                    </div>
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p4.png" alt="" className="layout_thumb_img" />
                                    </div>
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p5.png" alt="" className="layout_thumb_img" />
                                    </div>
                                    <div className="layout_thumb scaleup">
                                        <img src="/dist/images/project/tmeland/p6.png" alt="" className="layout_thumb_img" />
                                    </div>
                                </div>
                            </div>

                            <div className="brand_part">
                                <svg className="brand_part__logo brand_part__logo--tmeland"><use xlinkHref="#logo_tmeland"></use></svg>
                            </div>

                            <div className="project_part">
                                <div className="project_part__title">
                                    <div className="project_part__title_no">03.</div>
                                    <div className="project_part__title_con">App UI</div>
                                </div>
                                <section className="tiles tiles--columns">
                                    <div className="tiles_wrap">
                                        <div className="tiles_line">
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t1.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t2.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t3.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t4.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t5.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t6.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t7.png)" }}></div>
                                        </div>
                                        <div className="tiles_line">
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t8.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t9.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t10.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t11.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t12.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t13.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t14.png)" }}></div>
                                        </div>
                                        <div className="tiles_line">
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t15.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t16.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t17.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t18.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t19.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t20.png)" }}></div>
                                            <div className="tiles_line_img" style={{ backgroundImage: "url(/dist/images/project/tmeland/t1.png)" }}></div>
                                        </div>
                                    </div>
                                </section>
                            </div>
           
                            <div className="next_project" onClick={this.navigateNext}>
                                <div className="next_project__con">
                                    <h3 className="next_project__title">NEXT PROJECT</h3>
                                </div>
                                <div className="next_project__bg" style={{ backgroundImage: "url(/dist/images/project/qqmusic/cover.png)" }}></div>
                            </div>

                        </div>
                    </div>
                </div>
                <div className="project_bg">
                    <div className="project_scroll">
                        <div className="project_scroll__text">Scroll</div>
                        <div className="project_scroll__line">
                            <div className="project_scroll__line_el"></div>
                        </div>
                    </div>
                    <img src="/dist/images/project/tmeland/cover.png" alt="" className="project_bg__img" />
                </div>
            </>
        )
    }
}

export default withRouter(Tmeland)
