import React, { Component } from 'react'
import imagesLoaded from 'imagesloaded'
import { researchData } from '../../data/researchData'
import { Player } from 'video-react'
import "video-react/dist/video-react.css"

export default class GridDetail extends Component {

    gridDetailRef = React.createRef()
    componentDidMount() {
        this.reloadImage()
    }

    reloadImage(){
        this.imgLoad = imagesLoaded(this.gridDetailRef)
        this.imgLoad.on('progress', this.onProgress)
        // this.imgLoad.on( 'always', this.onAlways )
    }

    getArticleImages = (imageElem) => {
        // console.log(typeof imageElem)
        if (Array.isArray(imageElem)) {
            return imageElem.map((el, index) => {
                return <div className="grid_detail_thumb loading" key={index}>
                    <img src={el} key={index} alt="" className="grid_detail_thumb__img" />
                </div>
            })
        } else if (typeof imageElem === "string") {
            return <div className="grid_detail_thumb loading">
                <img src={imageElem} alt="" className="grid_detail_thumb__img" />
            </div>
        }
    }

    onProgress = (instance, image) => {
        if (image.isLoaded) {
            image.img.parentNode.classList.remove("loading")
        }
    }

    // onAlways=(instance)=>{
        
    // }

    render() {

        const { id } = this.props
        let detail_item = {}
        researchData && researchData.forEach(item => {
            if (item.id === id) {
                detail_item = item
                return
            }
        })
        const { subtitle, title, article } = detail_item

        return (
            <div className="grid_detail__content" ref={c => this.gridDetailRef = c}>
                <div className="grid_detail_subtit">{subtitle}</div>
                <div className="grid_detail_title">{title}</div>
                <div className="grid_detail_article">
                    {
                        article && article.map((item,index) => {
                            return <div className="grid_detail_part" key={index}>
                                {item.text && <p className="grid_detail_para">{item.text}</p>}
                                <div className="grid_detail_view">
                                    {this.getArticleImages(item.images)}
                                </div>
                                {item.video && <Player
                                    playsInline
                                    poster={item.video.poster}
                                    src={item.video.src}
                                />}
                            </div>
                        })
                    }

                </div>
            </div>
        )
    }
}
